import {
  Component,
  OnInit,
  Inject,
  Input,
  OnChanges,
  SimpleChanges,
  OnDestroy
} from '@angular/core';
import { GridOptions, GridApi, ColumnApi } from 'ag-grid/main';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { OpticalStoreService } from '@app/modules/optical-store/opticalStore.service';
import { OpticalStoreApiService, MOMENT, CLONEDEEP } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DatePipe } from '@angular/common';
import { MatDatepicker } from '@angular/material';
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl
} from '@angular/forms';
// tslint:disable-next-line:max-line-length
import { OpticalPointOfSaleDueListComponent } from './../optical-invoice-list/optical-point-of-sale-due-list/optical-point-of-sale-due-list.component';
// tslint:disable-next-line:max-line-length
import { OpticalReturnActionsListComponent } from '@app/modules/optical-store/containers/optical-point-of-sale/optical-return-list/optical-return-actions-list/optical-return-actions-list.component';
import { ReportsService } from '@core/services/reports-api.service';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
// @AutoUnsubscribe()
@Component({
  selector: 'app-pos-return',
  templateUrl: './optical-return-list.component.html'
})
export class PosReturnHistoryComponent implements OnChanges, OnDestroy {
  @Input()
  itemReturn: any;

  private sessionData: UserSession;
  private gridApi: GridApi;
  private gridColumnApi: ColumnApi;

  rowData: any;
  constructor(
    @Inject(MOMENT) private moment,
    userService: UserService,
    @Inject(CLONEDEEP) private cloner,
    public dialog: MatDialog,
    private opticalStoreService: OpticalStoreService,
    private reportService: ReportsService,
    private toasterService: ToastrService
  ) {
    this.sessionData = userService.getUser();
  }

  gridOptions: GridOptions = {
    columnDefs: [
      {
        headerName: 'Bill Number',
        field: 'opticalBillNo',
        cellStyle: { color: '#00a8dd' },
        onCellClicked: this.onCelllClicked.bind(this)
      },
      { headerName: 'PatientId', field: 'hosPatientId' },
      { headerName: 'Patient Name', field: 'patientNm' },
      { headerName: 'Remarks', field: 'returnRemarks' },
      {
        headerName: 'Return Date',
        field: 'returnDt',
        valueFormatter: this.agGridDateFormatter.bind(this)
      },
      { headerName: 'Service Amount', field: 'totalReturnServiceAmt' },
      { headerName: 'Total Tax', field: 'totalTaxAmt' },
      { headerName: 'Total Amount', field: 'totalAmt' },
      { headerName: 'Due Deduction Amt', field: 'dueDeductionAmt' },
      { headerName: 'Return Amount', field: 'totalReturnAmt' },
      {
        headerName: 'Actions',
        field: '',
        suppressFilter: true,
        cellRendererFramework: OpticalReturnActionsListComponent
      }
    ],
    context: {
      componentParent: this
    }
  };

  ngOnChanges() {
    if (this.itemReturn) {
      this.rowData = this.itemReturn.map(value => {
        const _value = this.cloner(value);
        _value.totalTaxAmt = Math.round(value.totaSgstAmt + value.totalCgstAmt);
        _value.totalAmt = Math.round(
          _value.totalReturnServiceAmt +
            (value.totaSgstAmt + value.totalCgstAmt)
        );
        return _value;
      });
    }
  }

  onCelllClicked(params) {
    const billData = params.data;
    billData.keyFrom = 'returnHistory';
    const dialogRef = this.dialog.open(OpticalPointOfSaleDueListComponent, {
      width: '50vw',
      data: billData,
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
      }
    });
  }

  agGridDateFormatter(params) {
    return this.moment(params.value).format('DD/MM/YYYY');
  }

  gridReady(params) {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
  }
  posReturnPrint(params) {
    const queryparams = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      opticalPointOfSaleReturnId: params.opticalPointOfSaleReturnId
    };
    this.reportService.fetchOpticalPosReturnsReport(queryparams).subscribe((response) => {
      if (response instanceof ArrayBuffer) {
        const ReturnRePrint = new Blob([response], {
          type: 'application/pdf'
        });
        const reportURL = URL.createObjectURL(ReturnRePrint);
        const win = window.open('', '_blank');
        win.location.href = reportURL;
        setTimeout(() => {
          win.print();
        }, 1000);
      } else if (response.responseCode === 'E400') {
        this.toasterService.error('Failure', response.responseMessage);
      }
    },
    () => {
      this.toasterService.error('failure', 'Something Went Wrong');
    }
    );
  }
  ngOnDestroy() {
  }
}
