import {
  Component,
  OnInit,
  ChangeDetectorRef,
  Output,
  EventEmitter,
  SimpleChanges,
  OnChanges,
  Input,
  OnDestroy
} from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material';
import { OsDialogComponent } from '@app/modules/optical-store/containers/os-dialog/os-dialog.component';
import { Observable, Subject, of, concat } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  switchMap,
  map,
  catchError,
  multicast
} from 'rxjs/operators';
import { OpticalStoreService } from '@app/modules/optical-store/opticalStore.service';
import { ToastrService } from 'ngx-toastr';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { OpticalStoreApiService, LoginApiService, UtilService } from '@core/services';
import { Store } from '@ngrx/store';
import { UserSession } from '@core/models/user-session';
import { ActivatedRoute, Params } from '@angular/router';
import { UserService } from '@core/services/user.service';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';

// @AutoUnsubscribe()

@Component({
  selector: 'app-frames',
  templateUrl: './frames.component.html',
  styleUrls: ['./frames.component.scss']
})
export class FramesComponent implements OnInit, OnChanges , OnDestroy {
  @Output()
  addToTable = new EventEmitter();
  @Input()
  clearPos;
  public objectKeys = Object.keys;
  posFormsForm: FormGroup;
  mdlSampleIsOpen: Boolean = false;
  showingLensTable: Boolean = true;
  isDisable: Boolean = true;
  brandNameList$: Observable<any>;
  brandNameListMuticased: Observable<any>;
  public brandNameListSubject: Subject<any>;
  public sessionData: UserSession;
  brandNameListinput$ = new Subject<string>();
  brandNames: any[] = [];
  barcodeData: any[] = [];
  public framesData = <any>{};
  public brandName: any;
  public serialNo;
  public orderType: any;
  public orderQty;
  public opticalItemType: any;
  batchList: any[] = [];
  name = this.utilService.generateUniqueId();

  constructor(
    public dialog: MatDialog,
    private changeDecRef: ChangeDetectorRef,
    private opticalService: OpticalStoreApiService,
    private componentOpticalStoreService: OpticalStoreService,
    private toaster: ToastrService,
    private formbuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    private store: Store<any>,
    private loginService: LoginApiService,
    private userService: UserService,
    private utilService: UtilService,
  ) {
    this.sessionData = userService.getUser();
  }
  ngOnInit() {
    this.componentOpticalStoreService.paymentData.next(null);
    this.activatedRoute.queryParams.subscribe(
      (params: Params) => {
        this.orderType = params['type'];
        console.log(this.orderType);
      });
    this.fetchBrandNames();
    // the fields should be disable for the sale and enable for the order
    if (this.orderType === 'order') {
      this.isDisable = false;
    }
  }
  ngOnChanges(changes: SimpleChanges) {
    if (changes.clearPos) {
      this.framesData = {};
      this.brandName = null;
      this.serialNo = null;
    }
  }



  getBarcode(type) {
    this.opticalItemType = this.componentOpticalStoreService.getCurrentOpticalItem(
      type
    );
    const framesBarcodeName = this.framesData.barcodeNm;

    if (this.framesData.barcodeNm) {
      const payload: any = {
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId,
        itemTypeId: this.opticalItemType.opticalTypeId
      };
      if (this.framesData.barcodeNm) {
        payload.barcode = this.framesData.barcodeNm;
      }
      this.opticalService
        .fetchBrandsOpticalPos(payload)
        .subscribe((response: any) => {
          if (response.responseCode === 'E200') {

            this.barcodeData = response.data;
          } else if (response.response === 'E400') {

          }
        });
    }
  }

  fetchBrandNames() {
    this.brandNameListSubject = new Subject();
    this.brandNameList$ = concat(
      of([]), // default items
      this.brandNameListinput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        filter(checkStringCount => {
          if (checkStringCount && checkStringCount.length > 1) {
            return true;
          }
        }),
        //  tap(() => this.people3Loading = true),
        switchMap((term: string) => {
          const payload: any = {
            createdUsrId: this.sessionData.regId,
            itemTypeId: this.opticalItemType.opticalTypeId,
            orgGrpId: this.sessionData.orgGroupId,
            orgId: this.sessionData.orgId,
            itemBrandNm: term
          };
          if (this.framesData.barcodeNm) {
            payload.barcode = this.framesData.bracodeNm;
          }
          return this.opticalService.fetchBrandsOpticalPos(payload).pipe(
            map((response: any) => {
              if (response.responseCode === 'E200') {
                return response.data;
              } else if (response.response === 'E400') {
                // return (this.manufacturersList = []);
              }
            }),
            catchError(error => {
              return of([]);
            })
          );
        })
      )
    );
    this.brandNameList$.subscribe(this.brandNameListSubject);
  }

  onAddBrand(brand) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      itemTypeId: this.opticalItemType.opticalTypeId,
      opticalItemId: brand.opticalItemId,
      itemBrandNm: brand.itemBrandNm
    };
    this.opticalService.fetchBatchesOpticalPos(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.batchList = response.data;

        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  onAddBatch(batch, batchType) {

    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      itemTypeId: this.opticalItemType.opticalTypeId,
      opticalInventoryId: batch.opticalInventoryId
    };
    this.opticalService.fetchInventoryList(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          if (batchType === 'Frame') {
            const data = response.data[0];
            data.barcodeNm = this.framesData.barcodeNm;
            this.framesData = data;
            if (this.framesData.serialNo) {
              this.framesData.quantity = 1;
            }
            this.framesData.tax = this.framesData.sgstTaxPercntg + this.framesData.cgstTaxPercntg;
          }
        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }


  addItemsToList() {
    if (
      this.isEmptyObject(this.framesData)
    ) {
      this.toaster.warning('Please Add Items');
      return;
    }
    if (!this.isEmptyObject(this.framesData)) {
      if ((!this.orderType) || (this.orderType === 'sale')) {
      if (
        !this.framesData.quantity ||
        isNaN(Number(this.framesData.quantity))
      ) {
        this.toaster.warning('Please Give Valid Quantity for Frames');
        return;
      } else if  (this.framesData.quantity > this.framesData.availableQty) {
        this.toaster.warning('Failure!', 'Quantity should not be more than Available Quantity');
        return;
      }
    }
      this.framesData.type = 'Frame';
      if (this.orderType === 'order') {
        this.framesData.orderQty = this.orderQty;
       if (!this.framesData.orderQty) {
        this.toaster.warning(
          'Failure!',
          'Please Enter Quantity'
        );
        return;
      }
      if (this.orderQty > this.framesData.availableQty) {
        this.toaster.warning('Failure!', 'Quantity should not be more than Available Quantity');
       return;
      }
    }
      this.addToTable.emit(this.framesData);
      this.orderQty = null;
      this.framesData = {};
      this.brandName = null;
      this.serialNo = null;
      this.batchList = [];
    }

  }

  // private openValuesModal(type: String): void {
  //   const dialogRef = this.dialog.open(OsDialogComponent, {
  //     data: type,
  //     disableClose: true
  //   });

  //   dialogRef.afterClosed().subscribe(result => {
  //     const index = this.spectaclesValuesTable.findIndex(element => element.eye === result.eye);
  //     this.spectaclesValuesTable[index] = result;
  //     this.changeDecRef.detectChanges();
  //   });
  // }

  isEmptyObject(obj) {
    for (const prop in obj) {
      if (obj.hasOwnProperty(prop)) {
        return false;
      }
    }
    return true;
  }

  onFocusBrand(ev, type) {

    if (this.framesData.barcodeNm) {
      this.getBarcode('Frame');
    }
    this.brandNameListSubject.next([]);
    this.opticalItemType = this.componentOpticalStoreService.getCurrentOpticalItem(
      type
    );

    this.brandNameListSubject.next(this.barcodeData);

  }

  ngOnDestroy() {
  }


}
