import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
  name: 'getAge'
})
export class GetAgePipe implements PipeTransform {
  transform(data: Date): any[] {
    if (!data) {
      return;
    }
    const birthDate = new Date(data);
    const today = new Date();
    const age = [];
    const y = [today.getFullYear(), birthDate.getFullYear()];
    let ydiff = y[0] - y[1];
    const m = [today.getMonth(), birthDate.getMonth()];
    let mdiff = m[0] - m[1];
    const d = [today.getDate(), birthDate.getDate()];
    let ddiff = d[0] - d[1];
    if (mdiff < 0 || (mdiff === 0 && ddiff < 0)) {
      --ydiff;
    }
    if (mdiff < 0 || (mdiff === 0 && ddiff < 0)) {
        mdiff += 12;
    }
    if (ddiff < 0) {
      birthDate.setMonth(m[1] + 1, 0);
      ddiff = birthDate.getDate() - d[1] + d[0];
      --mdiff;
    }
    return [ydiff, mdiff, ddiff];
   }
}
