import { Component, OnInit } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { RowNode, GridApi, ICellRendererParams } from 'ag-grid';
import { UserSession } from '@core/models/user-session';
import { PosReturnHistoryComponent } from '@app/modules/optical-store/containers/optical-point-of-sale/optical-return-list/optical-return-list.component';
import { OpticalStoreService } from '@app/modules/optical-store/opticalStore.service';

@Component({
  selector: 'app-optical-return-actions-list',
  templateUrl: './optical-return-actions-list.component.html',
  styleUrls: ['./optical-return-actions-list.component.scss']
})
export class OpticalReturnActionsListComponent implements ICellRendererAngularComp {

  public rowNode: RowNode;
  private api: GridApi;
  private params: ICellRendererParams;
  private selectedRows: any[] = [];
  private sessionData: UserSession;
  private parentComponent: PosReturnHistoryComponent;
  constructor(
    private componentOpticalStoreService: OpticalStoreService,
  ) {
   }

  agInit(params: ICellRendererParams): void  {
    this.api = params.api;
    this.rowNode = params.node;
    this.params = params;
    this.parentComponent = params.context.componentParent;
  }

  refresh(): boolean {
    return false;
  }
  returnSelectedRow() {
    this.params.context.componentParent.onReturnItem(this.rowNode);
  }
  printInvoice() {
    if (this.parentComponent) {
      this.parentComponent.posReturnPrint(this.params.data);
    }
  }

}
