import {
  Component,
  OnInit,
  NgModule,
  ViewChild,
  ChangeDetectorRef,
  ElementRef,
  OnDestroy
} from '@angular/core';
import {
  FormControl,
  FormGroup,
  FormBuilder,
  ReactiveFormsModule,
  FormsModule,
  Validators
} from '@angular/forms';
import { NgSelectModule, NgOption } from '@ng-select/ng-select';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription, Observable, of, concat, from } from 'rxjs';
import {
  distinctUntilChanged,
  debounceTime,
  switchMap,
  tap,
  catchError,
  map,
  count,
  filter
} from 'rxjs/operators';
import { UserSession } from '@core/models/user-session';
import {
  Router,
  ActivatedRouteSnapshot,
  ActivatedRoute
} from '@angular/router';
import { OpticalStoreService } from '../../../opticalStore.service';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { OpticalStoreApiService, UtilService } from '@core/services';
import { Store } from '@ngrx/store';
import * as S3 from 'aws-sdk/clients/s3';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { UserService } from '@core/services/user.service';

@AutoUnsubscribe()
@Component({
  selector: 'app-add-optical-suppliers',
  templateUrl: './add-optical-suppliers.component.html',
  styleUrls: ['./add-optical-suppliers.component.scss']
})
export class AddOpticalSuppliersComponent implements OnInit, OnDestroy {
  // items = [{name: true, id: 1}, {name : 'Two'}, {name : 3}];
  // private sessionData: UserSession;
  public manufacturersList: any[] = [];
  currentManufacturersList: any[] = [];
  selectedManufacturer: any;
  deliveryTime: any;
  addSupplierForm: FormGroup;
  vendorId: any;
  private sessionData: UserSession;
  localYes: Boolean = true;
  editVendorData: any;
  editVendor: Boolean;
  manufacturerStatus: Boolean = false;
  // manufacurersListSubscription: Subscription;
  manufacurersList$: Observable<any>;
  // manufacurersList = [];
  // people3$: Observable<Person[]>;
  // people3Loading = false;
  manufacurersListinput$ = new Subject<string>();
  public patient: any;

  public bucket = new S3(this.componentOpticalStoreService.getBucketsConfig());

  acceptedMimeTypes = ['image/gif', 'image/jpeg', 'image/png'];
  public imageUploadSubscription: Subscription;
  @ViewChild('fileInput') fileInput: ElementRef;
  public url;
  public imageFile;
  public supplierFormSubmitted = false;
  supplier_details_validation_messages = {
    vendorName: [
      { type: 'required', message: '* Supplier Name is required' },
      {
        type: 'minlength',
        message: '* Supplier Name must be at least 3 characters long'
      },
      {
        type: 'maxlength',
        message: '* Supplier Name cannot be more than 50 characters long'
      },
      { type: 'pattern', message: 'Supplier Name must contain only letters' }
    ],
    vendorLicence: [
      { type: 'required', message: '* Licence Id is required' },
      {
        type: 'minlength',
        message: '* Licence Id must be at least 3 characters long'
      },
      {
        type: 'maxlength',
        message: '* Licence Id cannot be more than 50 characters long'
      }
    ],
    vendorGST: [
      { type: 'required', message: '* GST No is required' },
      {
        type: 'minlength',
        message: '* GST No must be at least 3 characters long'
      },
      {
        type: 'maxlength',
        message: '* GST No cannot be more than 50 characters long'
      }
    ],
    vendorTAN: [
      { type: 'required', message: '* TAN No is required' },
      {
        type: 'minlength',
        message: '* TAN No must be at least 3 characters long'
      },
      {
        type: 'maxlength',
        message: '* TAN No cannot be more than 50 characters long'
      }
    ],
    narcosc: [
      {
        type: 'minlength',
        message: '* Narcosc must be at least 3 characters long'
      },
      {
        type: 'maxlength',
        message: '* Narcosc cannot be more than 50 characters long'
      }
    ],
    email: [{ type: 'pattern', message: 'Enter a valid email' }],
    address: [
      { type: 'required', message: '* Address is required' },
      { type: 'minlength', message: '* Address is Too Small' },
      { type: 'maxlength', message: '* Address is Too Long' }
    ],
    pincode: [
      { type: 'minlength', message: '* Pincode is Too Small' },
      { type: 'maxlength', message: '* Pincode is Too Long' },
      { type: 'pattern', message: 'Enter Valid Pincode' }
    ],
    cityName: [
      { type: 'required', message: '* City is required' },
      { type: 'minlength', message: '* City is Too Small' },
      { type: 'maxlength', message: '* City is Too Long' },
      { type: 'pattern', message: 'Enter a Valid City' }
    ],
    CountryName: [
      { type: 'required', message: '* Country is required' },
      { type: 'minlength', message: '* Country is Too Small' },
      { type: 'maxlength', message: '* Country is Too Long' },
      { type: 'pattern', message: 'Enter a Valid Country' }
    ],
    mobileNo: [
      { type: 'minlength', message: 'Contact No is Too Small' },
      { type: 'maxlength', message: 'Contact No is Too Long' },
      { type: 'pattern', message: 'Enter a Valid Contact No' }
    ],
    mobileNoalt: [
      { type: 'minlength', message: 'Contact No 2 is Too Small' },
      { type: 'maxlength', message: 'Contact No 2 is Too Long' },
      { type: 'pattern', message: 'Enter a Valid Contact No 2' }
    ]
  };
  constructor(
    private opticalService: OpticalStoreApiService,
    private componentOpticalStoreService: OpticalStoreService,
    private toaster: ToastrService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private formbuilder: FormBuilder,
    public dialog: MatDialog,
    private store: Store<any>,
    private changeDecRef: ChangeDetectorRef,
    private utilService: UtilService,
    userService: UserService,
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.fetchManufacturersList();
    this.initForm();
    this.getEditVendorData();
  }

  ngOnDestroy() { }
  initForm() {
    this.addSupplierForm = this.formbuilder.group({
      vendorNm: this.formbuilder.control('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(50),
        Validators.pattern(/^[a-zA-Z ]*$/)
      ]),
      vendorLicenseId: this.formbuilder.control('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(50)
      ]),
      narcoSc: this.formbuilder.control(null, [
        Validators.minLength(3),
        Validators.maxLength(50)
      ]),
      vendorGst: this.formbuilder.control('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(50)
      ]),
      vendorTan: this.formbuilder.control('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(50)
      ]),
      vendorContactNo1: this.formbuilder.control(null, [
        Validators.minLength(10),
        Validators.maxLength(10),
        Validators.pattern(/(5|6|7|8|9)\d{9}$/)
      ]),
      vendorContactNo2: this.formbuilder.control(null, [
        Validators.minLength(10),
        Validators.maxLength(10),
        Validators.pattern(/(5|6|7|8|9)\d{9}$/)
      ]),
      emailId: this.formbuilder.control(null, [
        Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)
      ]),
      webSite: this.formbuilder.control(null),
      description: this.formbuilder.control(null),
      vendorAddress1: this.formbuilder.control(null, [
        Validators.required,
        Validators.minLength(1),
        Validators.maxLength(300)
      ]),
      pincode: this.formbuilder.control(null, [
        Validators.minLength(6),
        Validators.maxLength(6),
        Validators.pattern(/^[1-9][0-9]{5}$/)
      ]),
      vendorCityNm: this.formbuilder.control(null, [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(20),
        Validators.pattern(/^[a-zA-Z ]*$/)
      ]),
      vendorDist: this.formbuilder.control(null),
      vendorState: this.formbuilder.control(null),
      vendorCountry: this.formbuilder.control('India', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(30),
        Validators.pattern(/^[a-zA-Z ]*$/)
      ])
    });
  }

  getEditVendorData() {
    this.editVendorData = this.componentOpticalStoreService.vendorData;
    if (this.editVendorData) {
      this.activatedRoute.queryParams.subscribe(params => {
        if (params['isEditable']) {
          this.addSupplierForm.patchValue(this.editVendorData);
          this.editVendor = true;
          this.vendorId = this.editVendorData.vendorId;
          this.localYes = this.editVendorData.isLocal;
          this.url =
            this.editVendorData.imagePath && this.editVendorData.imagePath[0];
          this.currentManufacturersList = this.editVendorData.manufacturerList
            ? this.editVendorData.manufacturerList
            : [];
        } else {
          this.componentOpticalStoreService.removeVendorData();
          this.editVendor = false;
        }
      });
    } else {
      this.editVendor = false;
    }
  }

  fetchManufacturersList() {
    this.manufacurersList$ = concat(
      of([]), // default items
      this.manufacurersListinput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        filter(checkStringCount => {
          if (checkStringCount.length > 1) {
            return true;
          }
        }),
        //  tap(() => this.people3Loading = true),
        switchMap((term: string) => {
          const payload = {
            orgId: this.sessionData.orgId,
            orgGrpId: this.sessionData.orgGroupId,
            createdUsrId: this.sessionData.regId,
            manufacturerNm: term
          };
          return this.opticalService.fetchManufacturersList(payload).pipe(
            map((response: any) => {
              if (response.responseCode === 'E200') {
                return (this.manufacturersList = response.data);
              } else if (response.response === 'E400') {
                return (this.manufacturersList = []);
              }
            }),
            catchError(error => {
              return of([]);
            })
          );
        })
      )
    );
  }

  addMnanufacturerToList() {
    if (typeof this.selectedManufacturer === 'string') {
      this.selectedManufacturer = { manufacturerNm: this.selectedManufacturer };
    }
    this.selectedManufacturer.expectedDeliveryTime = this.deliveryTime;
    this.currentManufacturersList.forEach(val => {
      if (val.manufacturerId && val.manufacturerId === this.selectedManufacturer.manufacturerId) {
        this.manufacturerStatus = true;
      }
    });
    if (this.manufacturerStatus === true) {
      this.toaster.error('Failure!', 'Manufacturer name already selected');
      this.manufacturerStatus = false;
    } else {
      this.manufacturerStatus = false;
      this.currentManufacturersList.push(this.selectedManufacturer);
    }
    //  this.currentManufacturersList.push(this.selectedManufacturer);
    this.selectedManufacturer = null;
    delete this.deliveryTime;
  }
  removeCurrentManufacturer(manufacturer, index) {
    if (this.editVendor) {
      const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        // data: 'Are You Sure Want To Unmap Manufacturer ?',
        data: { title: 'Are You Sure Want To Unmap Manufacturer ?' },
        disableClose: true
      });
      dialogRef.afterClosed().subscribe(result => {
        if (result === true) {
          (manufacturer.orgId = this.sessionData.orgId),
            (manufacturer.orgGrpId = this.sessionData.orgGroupId),
            (manufacturer.createdUsrId = this.sessionData.regId),
            this.opticalService
              .unMapManufacturerFromVendor(manufacturer)
              .subscribe(
                (response: any) => {
                  if (response.responseCode === 'E200') {
                    this.currentManufacturersList.splice(index, 1);
                    this.toaster.success('Success!', response.responseMessage);
                  } else if (response.response === 'E400') {
                    this.toaster.error('Failure!', response.responseMessage);
                  }
                },
                err => {
                  this.toaster.error('Failure!', 'Something went wrong');
                }
              );
        }
      });
    } else {
      this.currentManufacturersList.splice(index, 1);
    }
  }

  submitAddSupplierForm(value) {
    this.supplierFormSubmitted = true;
    if (this.currentManufacturersList.length === 0) {
      this.toaster.warning('Please select atleast one Manufacturer');
      return;
    }
    if (this.addSupplierForm.valid) {
      if (this.imageFile) {
        const bucketDetails = this.componentOpticalStoreService.getBucketName();
        if (this.editVendor && this.editVendorData.imagePath) {
          const params = {
            Bucket: bucketDetails.bucketURL,
            Key: this.editVendorData.imagePath[0].split('/')[4]
          };
          this.bucket.deleteObject(params, (error, data) => {
            if (data) {
              this.uploadImage();
            } else {
              this.toaster.error('Failure!', 'Something went wrong');
            }
          });
        } else {
          this.uploadImage();
        }
      } else {
        this.insertSupplierForm();
      }
    } else {
      this.validateAllFormFields(this.addSupplierForm);
    }
  }

  uploadImage() {
    const bucketDetails = this.componentOpticalStoreService.getBucketName();
    const key = this.utilService.generateUniqueId() + '-' + this.imageFile.name;
    const params = {
      Bucket: bucketDetails.bucketURL,
      Key: key,
      Body: this.imageFile
    };
    // const awsImgUpload =

    this.bucket
      .putObject(params)
      .promise()
      .then(() => {
        const imgUrl = bucketDetails.url + key;
        this.insertSupplierForm(imgUrl);
      })
      .catch(console.error);

    // this.imageUploadSubscription = from(awsImgUpload).subscribe(() => {
    //   const imgUrl = bucketDetails.url + key;
    //   this.insertSupplierForm(imgUrl);
    // });
  }

  insertSupplierForm(img?: string) {
    let url = 'insertVendor';
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      ...this.addSupplierForm.value
    };
    // payload.imagePath = img && [img];
    if (img) {
      payload.imagePath = [img];
    } else if (this.editVendor && this.editVendorData.imagePath) {
      payload.imagePath = this.editVendorData.imagePath;
    }
    if (this.editVendor) {
      payload.vendorId = this.vendorId;
      url = 'updateVendor';
    }
    payload.listOfManufactureReq = this.currentManufacturersList;
    payload.isLocal = this.localYes;
    payload.vendorTypeId = 332;
    this.opticalService[url](payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          if (!this.editVendor) {
            this.addSupplierForm.reset();
            // this.addSupplierForm.markAsPristine();
            // this.addSupplierForm.markAsUntouched();
            // this.addSupplierForm.updateValueAndValidity();
            this.supplierFormSubmitted = false;
            this.currentManufacturersList = [];
            this.url = '';
          }
          this.toaster.success('Success!', response.responseMessage);
          if (this.editVendor) {
            this.router.navigate(['/opticalStore/optical-all-suppliers']);
          }
        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', response.responseMessage);
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  submitUpdateSupplierForm(updateValues) {
    this.submitAddSupplierForm(updateValues);
    //  this.router.navigate(['/opticalStore/optical-all-suppliers']);
  }

  cancelSupplierEdit() {
    if (this.editVendor) {
      this.router.navigate(['/opticalStore/optical-all-suppliers']);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

  onFileChanged($event) {
    const file = this.fileInput.nativeElement.files[0];
    this.imageFile = this.fileInput.nativeElement.files[0];
    if (file && this.validateFile(file)) {
      const reader = new FileReader();
      reader.readAsDataURL(this.fileInput.nativeElement.files[0]);
      reader.onload = () => {
        this.url = reader.result;
        this.changeDecRef.detectChanges();
      };
    } else {
      this.toaster.warning(
        'Failure!',
        'File must be jpg, png, or gif and cannot be exceed 3 MB in size'
      );
    }
  }

  validateFile(file) {
    return this.acceptedMimeTypes.includes(file.type) && file.size < 3000000;
  }

}
