import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { Action } from '@ngrx/store';
import { Observable, of } from 'rxjs';

import {
  map,
  catchError,
  switchMap,
  mergeMap,
  concatMap
} from 'rxjs/operators';
import { OpticalStoreApiService } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { HttpErrorResponse } from '@angular/common/http';
import { UtilService } from '@core/services/utils.service';
import {
  FetchPatient,
  OpticalActionTypes,
  FetchPatientSuccess,
  FetchPatientError,
  FetchOpticalItemTypes,
  FetchOpticalItemTypesSuccess,
  FetchOpticalItemTypesError
} from '@app/modules/optical-store/actions';

@Injectable()
export class OpticalEffects {
  constructor(
    private utilService: UtilService,
    private action$: Actions,
    private opticalService: OpticalStoreApiService,
    private toastr: ToastrService
  ) {}

  @Effect()
  fetchPatient$: Observable<Action> = this.action$.pipe(
    ofType<FetchPatient>(OpticalActionTypes.fetchPatientOptical),
    mergeMap(action => {
      const payload = action.payload;
      return this.opticalService.fetchPatientOptical(payload).pipe(
        map((response: any) => {
          const { data } = response;
          if (response.responseCode === 'E200') {
            if (response.data.length === 0) {
              this.toastr.warning('No Data Found');
            }
            return new FetchPatientSuccess(data);
          } else if (response.responseCode === 'E400') {
            this.toastr.error('Failure!', 'Something went wrong');
            return new FetchPatientError(response);
          }
        }),
        catchError(error => {
          return of(new FetchPatientError(error));
        })
      );
    })
  );

  @Effect()
  fetchOpticalItems$: Observable<Action> = this.action$.pipe(
    ofType<FetchOpticalItemTypes>(OpticalActionTypes.fetchOpticalItemTypes),
    mergeMap(action => {
      const payload = action.payload;
      return this.opticalService.opticalMasterData(payload).pipe(
        map((response: any) => {
          const { data } = response;
          if (response.responseCode === 'E200') {
            return new FetchOpticalItemTypesSuccess(data);
          } else if (response.responseCode === 'E400') {
            this.toastr.error('Failure!', 'Something went wrong');
            return new FetchOpticalItemTypesError(response);
          }
        }),
        catchError(error => {
          return of(new FetchOpticalItemTypesError(error));
        })
      );
    })
  );
}
