
import {
  Action,
  ActionReducerMap,
  createFeatureSelector,
  createSelector,
  combineReducers
} from '@ngrx/store';
import { InjectionToken } from '@angular/core';
import { searchPatientReducer } from '@app/modules/optical-store/reducers/patient-search-optical';
import { opticalItemsReducer } from '@app/modules/optical-store/reducers/optical-item-types.reducer';
import { fetchOpticalVendorReducer } from '@app/modules/optical-store/reducers/optical-vendors.reducers';
import { fetchOpticalInventoryReducer } from '@app/modules/optical-store/reducers/optical-inventory.reducer';

export const reducers: ActionReducerMap<any> = {
  patientData: searchPatientReducer,
  opticalItemTypesData: opticalItemsReducer,
  opticalVendorsList: fetchOpticalVendorReducer,
  opticalInventoryList: fetchOpticalInventoryReducer
};

export const getOpticalState = createFeatureSelector<any>(
  'OPTICAL_FEATURE'
);

export const getSearchPatient = createSelector(getOpticalState, state =>
  state.patientData
);

export const getOpticalItemTypes = createSelector(getOpticalState, state =>
  state
);

export const getOpticalVendors = createSelector(getOpticalState, state =>
  state.opticalVendorsList
);

export const getOpticalInventory = createSelector(getOpticalState, state =>
  state.opticalInventoryList
);
