import { OpticalActions, OpticalActionTypes } from '@app/modules/optical-store/actions';
const initialState = {
  isLoading: false,
  errorOccured: false,
  data : []
};

export function searchPatientReducer(state = initialState, action: OpticalActions) {
  switch (action.type) {
    case OpticalActionTypes.fetchPatientOptical:
      return {
        isLoading: true,
        errorOccured: false,
        data : []
      };
    case OpticalActionTypes.fetchPatientOpticalSuccess:
     return {
        isLoading: false,
        errorOccured: false,
        data : action.payload
      };
    case OpticalActionTypes.fetchPatientOpticalError:
    return {
      isLoading: false,
      errorOccured: true,
      data : []
    };
    default:
      return state;
  }
}
