import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { MatDatepicker, MatSidenav, MatDialog } from '@angular/material';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { OpticalStoreApiService, UserService } from '@core/services';
import { OpticalStoreService } from '../../opticalStore.service';
import { UserSession } from '@core/models/user-session';
import { DatePipe } from '@angular/common';
import { ToastrService } from 'ngx-toastr';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { CommentsDialogComponent } from '@shared/components/comments-dialog/comments-dialog.component';
import { ReportsService } from '@core/services/reports-api.service';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
// @AutoUnsubscribe()
@Component({
  selector: 'app-optical-pending-orders',
  templateUrl: './optical-pending-orders.component.html',
  styleUrls: ['./optical-pending-orders.component.scss']
})
export class OpticalPendingOrdersComponent implements OnInit, OnDestroy {
  dateMax = new Date();
  dateMin;
  @ViewChild('sidenav')
  sidenav: MatSidenav;

  @ViewChild('lensnav')
  lensnav: MatSidenav;

  opticalPendingOrdersForm: FormGroup;
  insufficientStockKey;
  editKey;
  orderKey: string;
  fromDt: any;
  toDt: any;
  defaultDate = new Date();
  private sessionData: UserSession;
  maxDate: any;
  pendingOrdersList: any;
  isFetchingData = false;
  mobileNumber: any;
  opticalItemOrderIdNo: any;
  itemStatus: void;
  framesList: any = [];
  contactLens: any = [];
  lensList: any = [];
  fetchOpticalDetailsList: any = [''];
  patientNm: string;
  // isClickable = true;

  constructor(
    private opticalPendingOrdersFormBuilder: FormBuilder,
    private componentOpticalService: OpticalStoreService,
    private opticalService: OpticalStoreApiService,
    private userService: UserService,
    private datePipe: DatePipe,
    private toaster: ToastrService,
    private dialog: MatDialog,
    private reportsService: ReportsService,
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.fromDt = this.datePipe.transform(this.defaultDate, 'yyyy-MM-dd');
    this.toDt = this.datePipe.transform(this.defaultDate, 'yyyy-MM-dd');
    this.componentOpticalService.insufficientStockKey.subscribe(data => {
      if (data === true) {
      this.pendingOrders();
      }
    });
    this.pendingOrders();
    this.initForm();
  }
  initForm() {
    this.opticalPendingOrdersForm = this.opticalPendingOrdersFormBuilder.group({
      fromDt: this.fromDt,
      toDt: this.toDt
    });
    this.opticalPendingOrdersForm.get('fromDt').valueChanges.subscribe(data => {
      this.maxDate = data;
      this.fromDt = this.datePipe.transform(data, 'yyyy-MM-dd');
    });
    this.opticalPendingOrdersForm.get('toDt').valueChanges.subscribe(data => {
      this.toDt = this.datePipe.transform(data, 'yyyy-MM-dd');
      if (data) {
        if (this.fromDt && this.toDt) {
          this.pendingOrders();
        }
      }
    });
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }

  order(list) {

    if (list.orderStatus === 'ORDERED') {
      this.editKey = 'order';
    this.componentOpticalService.editKey.next(this.editKey);
    this.orderSideNav();
    this.componentOpticalService.editData.next(list);
    }
  }
  edit(list) {
    this.orderKey = 'edit';
    this.componentOpticalService.orderKey.next(this.orderKey);
    this.orderSideNav();
    this.componentOpticalService.editData.next(list);
  }

  orderSideNav() {
    this.sidenav.open();
  }
  /**
   * @description This method is used to fetch pending orders list
   */
  pendingOrders() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      fromDate: this.fromDt,
      toDate: this.toDt
    };
    this.opticalService.fetchPendingOrders(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.pendingOrdersList = response.data;
          if (this.pendingOrdersList.length) {
            this.isFetchingData = false;
          } else {
            this.isFetchingData = true;
          }
        } else if (response.responseCode === 'E400') {
          this.toaster.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }
  /**
   * @description This method is used to send print for a particular order
   */
  printPendingOrders(list) {
    const queryparams = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      opticalItemOrderId: list.opticalItemOrderId
    };
    this.reportsService.fetchOpticalOrderSaleReport(queryparams).subscribe(
      response => {
        if (response instanceof ArrayBuffer) {
          const orderRePrint = new Blob([response], {
            type: 'application/pdf'
          });
          const reportURL = URL.createObjectURL(orderRePrint);
          const win = window.open('', '_blank');
          win.location.href = reportURL;
          setTimeout(() => {
            win.print();
          }, 1000);
        } else if (response.responseCode) {
          this.toaster.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }
  editOrder(list) {

  }

  /**
   * @description This method is used to send sms for a particular order
   */
  smsOrders(list) {
    this.mobileNumber = list.mobileNo;
    this.opticalItemOrderIdNo = list.opticalItemOrderIdNo;
    this.itemStatus = list.itemStatus;
    this.patientNm = list.patientNm;
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      orgNm: this.sessionData.orgList[0].orgName,
      createdUsrId: this.sessionData.regId,
      mobileNo: list.mobileNo,
      itemStatus: this.itemStatus,
      patientNm: this.patientNm,
      opticalItemOrderIdNo: this.opticalItemOrderIdNo,
      orderNo: list.opticalOrderNo

    };
    this.opticalService.sendOrderDetailsSmsToUser(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.toaster.success('success', response.responseMessage);
        } else if (response.responseCode === 'E400') {
          this.toaster.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }
  /**
   * @description This method is used to send sms for a particular order
   */
  deleteOrders(list, i) {
    this.fetchOpticalDetailsList = list;
    this.framesList = this.fetchOpticalDetailsList.insertOpticalOrdersFrameAndContactLensItemsReqAndRes;
    this.lensList = this.fetchOpticalDetailsList.opticalContactLensItemsReqAndRes;
    const dialogRef = this.dialog.open(CommentsDialogComponent, {
      data: {
        title: 'Are You Sure Want To Delete ? ',
        data: 'Reason'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const payload = {
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          createdUsrId: this.sessionData.regId,
          cancelReason: result,
          cancelUsrId: this.sessionData.regId,
          hosPatientId: list.hosPatientId,
          opticalInventoryId: this.fetchOpticalDetailsList.insertOpticalOrdersFrameAndContactLensItemsReqAndRes[0].opticalInventoryId || 0,
          opticalItemOrderIdNo: this.fetchOpticalDetailsList.opticalItemOrderIdNo,
          opticalItemOrderId: this.fetchOpticalDetailsList.opticalItemOrderId,
          opticalOrderNo: list.opticalOrderNo,
          orderQty: this.fetchOpticalDetailsList.insertOpticalOrdersFrameAndContactLensItemsReqAndRes[0].orderQty,
          patientId: list.patientId,
          totalPaidAmt: list.paidAmt
        };
        this.opticalService.deleteOrders(payload).subscribe(
          response => {
            if (response.responseCode === 'E200') {
              this.pendingOrdersList.splice(i, 1);
              this.toaster.success('success', response.responseMessage);
            } else if (response.responseCode === 'E400') {
              this.toaster.error('Failure!', response.responseMessage);
            }
          },
          () => {
            this.toaster.error('Failure!', 'Something went wrong');
          }
        );
      }
    });
  }

  /**
   * @description This method is used to opening the lens details page
   */
  openLensDetails(list) {
    this.lensSideNav();
    this.componentOpticalService.lensInvData.next(list);

  }

  lensSideNav() {
    this.lensnav.open();
  }
  ngOnDestroy() {
  }
}
