import { Component, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { AgRendererComponent, ICellRendererAngularComp } from 'ag-grid-angular';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ICellRendererParams } from 'ag-grid/dist/lib/rendering/cellRenderers/iCellRenderer';
import { RowNode } from 'ag-grid/dist/lib/entities/rowNode';
import { GridApi } from 'ag-grid/dist/lib/gridApi';
import { OnChanges, DoCheck } from '@angular/core/src/metadata/lifecycle_hooks';
import { VendorInvoiceModalComponent } from './vendor-bill-modal.component';
import { ToastrService } from 'ngx-toastr';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
// @AutoUnsubscribe()
@Component({
  selector: 'app-vendor-bill',
  templateUrl: './vendor-invoice-bill-dialog.component.html',
  styleUrls: ['./vendor-invoice-bill-dialog.component.scss'],
  encapsulation: ViewEncapsulation.Emulated
})
export class VendorInvoiceBillComponent implements ICellRendererAngularComp {
  public headOffice: boolean;
  private rowNode: RowNode;
  private api: GridApi;
  private params;

  constructor(private cdref: ChangeDetectorRef, public dialog: MatDialog,  private toaster: ToastrService) {}
  private selectData = <any>{};

  agInit(params: ICellRendererParams) {
    this.api = params.api;
    this.rowNode = params.node;
    this.selectData = params.data;
    console.log(this.selectData, '======');
    this.params = params;
  }

  refresh() {
    return false;
  }

  modalOpen() {
    if (this.selectData.totalDueAmt !== 0) {
      const dialogRef = this.dialog.open(VendorInvoiceModalComponent, {
        data: {
          getData: this.selectData,
          type: 'InventoryInvoice'
        },
        disableClose: true
      });
      dialogRef.afterClosed().subscribe(result => {
        this.params.context.componentParent.fetchInvoiceHistoryForm();
      });
    } else {
      this.toaster.warning('Failure!', 'Due Amount is Zero');
    }
  }
}
