import { NgModule } from '@angular/core';
import { Routes, RouterModule, Router } from '@angular/router';
import { OpticalPointOfSaleComponent } from './containers/optical-point-of-sale/optical-point-of-sale.component';
import { OpticalPurchaseOrderComponent } from './containers/optical-purchase-order/optical-purchase-order.component';
import { OpticalCommonComponent } from './containers/optical-common/optical-common.component';
import { AllOpticalSuppliersComponent } from './containers/optical-vendor/all-optical-suppliers/all-optical-suppliers.component';
import { AddOpticalSuppliersComponent } from './containers/optical-vendor/add-optical-suppliers/add-optical-suppliers.component';
import { AddOpticalInventoryComponent } from '@app/modules/optical-store/containers/optical-inventory/add-optical-inventory/add-optical-inventory.component';
import { AllOpticalInventoryComponent } from '@app/modules/optical-store/containers/optical-inventory/all-optical-inventory/all-optical-inventory.component';
import { NewInvoiceComponent } from '@app/modules/optical-store/containers/optical-vendor-invoice/new-invoice/new-invoice.component';
import { InvoiceHistoryComponent } from '@app/modules/optical-store/containers/optical-vendor-invoice/invoice-history/invoice-history.component';
import { OpticalInvoiceListComponent } from '@app/modules/optical-store/containers/optical-point-of-sale/optical-invoice-list/optical-invoice-list.component';
import { PosReturnHistoryComponent } from './containers/optical-point-of-sale/optical-return-list/optical-return-list.component';
import { OpticalPendingOrdersComponent } from '@app/modules/optical-store/containers/optical-pending-orders/optical-pending-orders.component';
import { OpticalBillingComponent } from '@app/modules/optical-store/containers/optical-point-of-sale/optical-billing/optical-billing.component';
export const OpticalStoreRoutings: Routes = [
  {
    path: '',
    component: OpticalCommonComponent,
    children: [
      { path: 'optical-point-of-sale', component: OpticalPointOfSaleComponent },
      {
        path: 'optical-point-of-order',
        component: OpticalPointOfSaleComponent
      },

      {
        path: 'optical-all-suppliers',
        component: AllOpticalSuppliersComponent
      },
      {
        path: 'optical-add-suppliers',
        component: AddOpticalSuppliersComponent
      },
      {
        path: 'optical-purchase-orders',
        component: OpticalPurchaseOrderComponent
      },
      {
        path: 'optical-all-inventory',
        component: AllOpticalInventoryComponent
      },
      {
        path: 'optical-add-inventory',
        component: AddOpticalInventoryComponent
      },
      { path: 'optical-new-invoice', component: NewInvoiceComponent },
      {
        path: 'optical-pending-orders',
        component: OpticalPendingOrdersComponent
      },
      {
        path: 'optical-pending-orders/optical-point-of-sale',
        component: OpticalPointOfSaleComponent
      },
      { path: 'optical-invoice-history', component: InvoiceHistoryComponent },
      {
        path: 'optical-pos-invoice',
        component: OpticalInvoiceListComponent,
        data: {
          title: 'Pos Invoice'
        },
        children: [
          {
            path: 'pos-return-history',
            component: PosReturnHistoryComponent
          }
        ]
      }
    ]
  },
  { path: '', redirectTo: 'optical-point-of-sale', pathMatch: 'prefix' }
];

@NgModule({
  imports: [RouterModule.forChild(OpticalStoreRoutings)],
  exports: [RouterModule]
})
export class OpticalStoreRoutingModule {}
