import { Injectable, Inject, OnInit, OnDestroy } from '@angular/core';
// import { OPTICALSTORE_END_POINTS } from './opticalStore.api';
import { ApiService } from '@core/services/api.service';
import { Observable, Subscription, BehaviorSubject } from 'rxjs';
import { SESSION_STORAGE, StorageService } from 'angular-webstorage-service';
import { Store, select } from '@ngrx/store';
import { PDFMAKE } from '@core/services';
import { getOpticalItemTypes } from '@app/modules/optical-store/reducers';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { APP_CONSTANTS } from '@app/app.constant';
import { AppConstant } from '@core/models/app-constant';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { last } from 'rxjs/operators';
@Injectable()
@AutoUnsubscribe()
export class OpticalStoreService  {
  private opticalItemTypes$: Observable<any>;
  private opticalItemTypesSubscription: Subscription;
  private sessionData: UserSession;
  private patientList;
  private organizationInfo = {};
  constructor(
    // @Inject(OPTICALSTORE_END_POINTS) private endPoints,
    @Inject(SESSION_STORAGE) private storage: StorageService,
    @Inject(PDFMAKE) private pdfmake,
    @Inject(APP_CONSTANTS) private appConstants: AppConstant,
    private api: ApiService,
    userService: UserService,
    private store: Store<any>
  ) {
    this.opticalItemTypes$ = this.store.pipe(select(getOpticalItemTypes));
    this.sessionData = userService.getUser();
    this.organizationInfo = this.sessionData.orgList;
  }
  private loggedUser: any;
  private vendorDetails: any;
  private inventoryDetails: any;
  private opticalItems: any = [];
  public insufficientStockKey = new BehaviorSubject(null);
  public editKey = new BehaviorSubject(null);
  public orderKey = new BehaviorSubject(null);
  public editData = new BehaviorSubject(null);
  public lensInvData = new BehaviorSubject(null);
  public patientData = new BehaviorSubject(null);
  public selectionType = new BehaviorSubject(null);
  public paymentData = new BehaviorSubject(null);
  public showsBilling =  new BehaviorSubject(null);
  public opticalCommonData =  new BehaviorSubject(null);

  getOpticalItemTypes() {
    this.opticalItemTypesSubscription = this.opticalItemTypes$
      // .pipe(last())
      .subscribe(
        data => {
          if (data.opticalItemTypesData.data.length > 0) {
            this.opticalItems = data.opticalItemTypesData.data;
            this.opticalItemTypesSubscription.unsubscribe();
          }
        }
      );
  }

  getCurrentOpticalItem(meuType) {
    return this.opticalItems.filter(item => item.opticalCode === meuType)[0];
  }

  // set loggedUserData(data) {
  //   this.loggedUser = data;
  //   this.storage.set('loggedUser', data);

  // }

  // get loggedUserData() {
  //   return this.storage.get('loggedUser');
  // }
  getPatientData() {
   return this.patientList;
  }
  setPatientData(data) {
    this.patientList = data;
  }

  set vendorData(data) {
    this.vendorDetails = data;
  }

  get vendorData() {
    return this.vendorDetails;
  }

  removeVendorData() {
    delete this.vendorDetails;
  }

  set inventoryData(data) {
    this.inventoryDetails = data;
  }

  get inventoryData() {
    return this.inventoryDetails;
  }

  removeinventoryData() {
    delete this.inventoryDetails;
  }
  getPrintSettings() {
    const settings = {
      pageSettings: {
        pageSize: 'A4',
        pageOrientation: 'portrait',
        margins: [15, 15, 15, 15]
      },
      hzHeader: {
        isTextRequired: true,
        isFirstPageOnly: false,
        isHeaderRequired: true,
        docInfo: {
          title: 'Invoice',
          fontSize: 25,
          fontColor: '#444444'
        },
        company: {
          fontColor: '#444444',
          fontSize: 9
        },
        companyAddress: {
          fontColor: '#444444',
          fontSize: 9
        },
        billTo: {
          billToColor: '#444444',
          billTofontSize: 9,
          addressColor: '#444444',
          addressFontSize: 9
        },
        invoice: {
          fontSize: 9,
          fontColor: '#444444'
        }
      },
      footer: {
        isLastPageOnly: false,
        isTextRequired: true,
        isFooterRequired: false,
        fontColor: '#444444',
        footerText: 'I am a cool footer'
      },
      itemTable: {
        itemHeader: {
          fontSize: 9,
          backgroundColor: '#444444',
          fontColor: '#ffffff'
        },
        itemRow: {
          fontSize: 9,
          backgroundColor: '#ffffff',
          fontColor: '#444444'
        }
      }
    };

    return settings;
  }

  getInvoicePrint(data, config) {
    const documentDef: any = {
      content: [],
      images: {},
      styles: {}
    };

    // config = angular.copy(config || DefaultInvoiceConfig.invoice);
    // settings up the page properties
    documentDef.pageSize = config.pageSettings.pageSize;
    documentDef.pageOrientation = config.pageSettings.pageOrientation;
    documentDef.pageMargins = config.pageSettings.margins;

    this.setUpStyle(documentDef, config);

    if (config.hzHeader.isHeaderRequired) {
      if (config.hzHeader.isFirstPageOnly) {
        documentDef.content.push(this.getInvoiceHeader(data, config));
      } else {
        // need to adjust margin to account for header height
        documentDef.header = this.getInvoiceHeader(data, config);
        if (config.hzHeader.isTextRequired) {
          documentDef.pageMargins[1] += 220;
        } else {
          documentDef.pageMargins[1] += 290;
        }
      }
    }
    documentDef.content.push(this.getInvoiceTable(data.tableData));
    documentDef.content.push(this.getInvoiceSummary(data, config)); // only needed in invoice
    documentDef.content.push({
      // text: data.totAmtInWords || '',
      text: [
        { text: 'Amount In Words: ' },
        { text: data.totAmtInWords }
      ],
      alignment: 'center',
      color: '#444',
      italics: true,
      fontSize: 10
    });
    documentDef.content.push(this.getSignature(data)); // only needed in invoice

    if (
      config.footer.footerBannerImage ||
      (config.footer.footerText && config.footer.isFooterRequired)
    ) {
      if (config.footer.isLastPageOnly) {
        documentDef.content.push(this.getPageFooter(data, config));
      } else {
        documentDef.footer = this.getPageFooter(data, config);
        if (config.footer.isTextRequired) {
          documentDef.pageMargins[3] += 12;
        } else {
          documentDef.pageMargins[3] += 50;
        }
      }
    }

    this.pdfmake.createPdf(documentDef).print();
  }

  /**
   * @description makes pages header
   * @param {Object} data contains printing information
   * @param {Object} config configuration object
   */
  getInvoiceHeader(data, config) {
    const header = {
      stack: []
    };
    // if top and middle header is required or not
    if (config.hzHeader.isHeaderRequired) {
      // header #1
      header.stack.push(this.getInvoiceHeaderTop(data, config));
      // header #2
      header.stack.push(this.getInvoiceHeaderMiddle(data, config));
    }
    // header #3
    header.stack.push(this.getInvoiceHeaderBottom(data, config));

    return header;
  }

  getInvoiceHeaderTop(data, config) {
    if (config.hzHeader.isTextRequired) {
      return {
        table: {
          widths: ['*', '*'],
          body: [
            [
              {
                image: config.hzHeader.profileLogo,
                height: 60,
                width: 60
              },
              {
                text: config.hzHeader.docInfo.title,
                alignment: 'right',
                style: 'documentTitleStyle'
              }
            ]
          ]
        },
        layout: 'noBorders',
        margin: [
          config.hzHeader.isFirstPageOnly ? 0 : config.pageSettings.margins[0],
          config.hzHeader.isFirstPageOnly ? 0 : config.pageSettings.margins[1],
          config.hzHeader.isFirstPageOnly ? 0 : config.pageSettings.margins[2],
          5
        ]
      };
    } else {
      return this.getHeaderBanner(data, config);
    }
  }

  getInvoiceHeaderMiddle(data, config) {
    const middleHeader = <any>{
      table: {
        widths: ['auto', '*', 'auto'],
        body: [
          [
            {
              stack: [
                {
                  text: data.header.companyName,
                  bold: true,
                  style: 'company'
                }
              ],
              alignment: 'left',
              color: '#444',
              fontSize: 12
            },
            ''
          ]
        ]
      },
      layout: 'noBorders',
      margin: [
        config.hzHeader.isFirstPageOnly ? 0 : config.pageSettings.margins[0],
        0,
        config.hzHeader.isFirstPageOnly ? 0 : config.pageSettings.margins[2],
        0
      ]
    };
    let i, j;

    middleHeader.table.body[0].push({
      stack: [
        {
          text:
            (data.header.due && !data.header.approximate && 'Balance Due') ||
            (data.header.approximate && 'Approx Due Amount') ||
            '',
          bold: true,
          fontSize: 13
        },
        {
          // text: data.header.due || data.header.approximate ||'',
          text:
            (data.header.due && 'Rs. ' + data.header.due) ||
            '' ||
            ((data.header.approximate && 'Rs. ' + data.header.approximate) ||
              ''),
          fontSize: 24
        }
      ],
      alignment: 'right',
      fontSize: 12,
      color: '#444' // configurable
    });

    for (i = 0, j = data.header.companyAddress.length; i < j; i++) {
      middleHeader.table.body[0][0].stack.push({
        text: data.header.companyAddress[i],
        style: 'companyAddress'
      });
    }
    return middleHeader;
  }

  getInvoiceHeaderBottomLab(data, config) {
    const headerBottom = <any>{
      table: {
        widths: ['auto', '*', 'auto'],
        body: [
          [
            {
              stack: [
                {
                  text: 'Bill To',
                  bold: true,
                  margin: [0, 3, 0, 3],
                  style: 'billTo'
                }
              ]
            },
            '',
            {
              table: {
                widths: ['auto', 'auto'],
                body: [
                  [
                    {
                      text:
                        ((data.header.approximate ||
                          data.header.approximate === 0) &&
                          'Approx Bill Id:') ||
                        'Bill ID: ',
                      // text: 'Invoice ID: ',
                      margin: [0, 2, 0, 0],
                      color: '#444',
                      alignment: 'right'
                    },
                    {
                      text: data.invoice.id,
                      style: 'invoice',
                      margin: [0, 2, 0, 0],
                      alignment: 'right'
                    }
                  ],
                  [
                    {
                      text: 'Bill Date: ',
                      margin: [0, 2, 0, 0],
                      color: '#444',
                      alignment: 'right'
                    },
                    {
                      text: data.invoice.date,
                      margin: [0, 2, 0, 0],
                      alignment: 'right',
                      color: '#444'
                    }
                  ]
                ]
              },
              fontSize: 9,
              layout: 'noBorders'
            }
          ]
        ]
      },
      layout: 'noBorders',
      margin: [
        config.hzHeader.isFirstPageOnly ? 0 : config.pageSettings.margins[0],
        0,
        config.hzHeader.isFirstPageOnly ? 0 : config.pageSettings.margins[2],
        10
      ]
    };
    let i, j;
    for (i = 0, j = data.billTo.address.length; i < j; i++) {
      headerBottom.table.body[0][0].stack.push({
        text: data.billTo.address[i],
        style: 'billToAddress'
      });
    }
    return headerBottom;
  }
  getInvoiceHeaderBottom(data, config) {
    const headerBottom = <any>{
      table: {
        widths: ['auto', '*', 'auto'],
        body: [
          [
            {
              stack: [
                {
                  text: 'Bill To',
                  bold: true,
                  margin: [0, 3, 0, 3],
                  style: 'billTo'
                }
              ]
            },
            '',
            {
              table: {
                widths: ['auto', 'auto'],
                body: [
                  [
                    {
                      text:
                        ((data.header.approximate ||
                          data.header.approximate === 0) &&
                          'Approx Bill Id:') ||
                        'Invoice ID: ',
                      // text: 'Invoice ID: ',
                      margin: [0, 5, 0, 0],
                      color: '#444'
                    },
                    {
                      text: data.invoice.id,
                      style: 'invoice',
                      margin: [0, 5, 0, 0],
                      alignment: 'right'
                    }
                  ],
                  [
                    {
                      text: 'Bill Date: ',
                      margin: [0, 2, 0, 0],
                      color: '#444'
                    },
                    {
                      text: data.invoice.date,
                      margin: [0, 2, 0, 0],
                      alignment: 'right',
                      color: '#444'
                    }
                  ],
                  [
                    {
                      colSpan: 2,
                      image: data.invoice.invoiceBarcode,
                      width: 200,
                      margin: [0, 2, 0, 0],
                      alignment: 'right'
                    },
                    ''
                  ]
                ]
              },
              fontSize: 9,
              layout: 'noBorders'
            }
          ]
        ]
      },
      layout: 'noBorders',
      margin: [
        config.hzHeader.isFirstPageOnly ? 0 : config.pageSettings.margins[0],
        0,
        config.hzHeader.isFirstPageOnly ? 0 : config.pageSettings.margins[2],
        10
      ]
    };
    let i, j;
    for (i = 0, j = data.billTo.address.length; i < j; i++) {
      headerBottom.table.body[0][0].stack.push({
        text: data.billTo.address[i],
        style: 'billToAddress'
      });
    }
    return headerBottom;
  }

  getInvoiceTable(tableData) {
    const tableBody = tableData.map(function (tableRow, index) {
      let mappedTableRow;
      if (index === 0) {
        mappedTableRow = tableRow.map(function (tableCell) {
          return {
            text: tableCell,
            style: 'itemHeader'
          };
        });
      } else {
        mappedTableRow = tableRow.map(function (tableCell) {
          return tableCell;
        });
      }
      return mappedTableRow;
    });
    const widths = [];
    let i, j;
    for (i = 0, j = tableBody[0].length; i < j; i++) {
      i === 1 ? widths.push('*') : widths.push('auto');
    }
    return {
      table: {
        headerRows: 1,
        widths: widths,
        body: tableBody
      },
      layout: {
        hLineWidth: function (i, node) {
          return i === 0 || i === 1 || i === node.table.body.length ? 0 : 1;
        },
        vLineWidth: function (i, node) {
          return 0;
        },
        hLineColor: function (i, node) {
          return '#ddd';
        },
        paddingLeft: function (i, node) {
          return 10;
        },
        paddingRight: function (i, node) {
          return 10;
        },
        paddingTop: function (i, node) {
          return 3;
        },
        paddingBottom: function (i, node) {
          return 3;
        }
      }
    };
  }

  getInvoiceSummary(data, config) {
    const invoiceSummary = {
      table: {
        widths: ['*', 'auto'],
        body: []
      },
      layout: {
        hLineWidth: function (i, node) {
          if (i === 0) {
            return 1;
          }
          return 0;
        },
        hLineColor: function (i, node) {
          if (i === 0) {
            return '#ddd';
          }
        },
        vLineWidth: function (i, node) {
          return 0;
        },
        paddingLeft: function (i, node) {
          return 10;
        },
        paddingRight: function (i, node) {
          return 10;
        },
        paddingBottom: function (i, node) {
          return 5;
        },
        paddingTop: function (i, node) {
          if (i === 0) {
            return 15;
          } else {
            return 2;
          }
        }
      },
      color: '#444',
      alignment: 'right',
      fontSize: 10,
      margin: [0, 10, 0, 0]
    };
    let i, j;
    for (i = 0, j = data.invoiceSummary.length; i < j; i++) {
      invoiceSummary.table.body.push([
        {
          text: data.invoiceSummary[i][0]
        },
        {
          text: data.invoiceSummary[i][1]
        }
      ]);
    }
    return invoiceSummary;
  }

  getSignature(data) {
    return {
      columns: [
        {
          text: 'Signature',
          alignment: 'right',
          fontSize: 13
        }
      ],
      margin: [0, 20, 0, 20],
      color: '#444'
    };
  }

  getPageFooter(data, config) {
    // data.footerText.footerText
    let footer, footerYOffset, footerWidth;

    if (config.footer.isTextRequired) {
      footer = {
        text: config.footer.footerText,
        alignment: 'center',
        fontSize: 9,
        style: 'footer'
      };
    } else {
      footer = {
        image: config.footer.footerBannerImage,
        height: 77,
        margin: [
          config.footer.isLastPageOnly ? -config.pageSettings.margins[0] : 0,
          config.footer.isLastPageOnly ? 20 : 0,
          config.footer.isLastPageOnly ? -config.pageSettings.margins[2] : 0,
          0
        ]
      };
      if (config.pageSettings.pageOrientation === 'landscape') {
        if (config.pageSettings.pageSize === 'A4') {
          footerWidth = 841;
          footerYOffset = 518;
        } else {
          footerWidth = 1008;
          footerYOffset = 535;
        }
      } else {
        // portrait
        if (config.pageSettings.pageSize === 'A4') {
          footerWidth = 596;
          footerYOffset = 400;
        } else {
          footerWidth = 612;
          footerYOffset = 400;
        }
      }
      footer.width = footerWidth;
    }
    return footer;
  }

  getHeaderBanner(data, config) {
    const headerTop: any = {
      image: config.hzHeader.headerBannerImage,
      height: 65.1968,
      margin: [
        config.hzHeader.isFirstPageOnly ? -config.pageSettings.margins[0] : 0,
        config.hzHeader.isFirstPageOnly ? -config.pageSettings.margins[1] : 0,
        config.hzHeader.isFirstPageOnly ? -config.pageSettings.margins[2] : 0,
        0
      ]
    };
    if (config.pageSettings.pageOrientation === 'landscape') {
      if (config.pageSettings.pageSize === 'A4') {
        headerTop.width = 841;
      } else {
        headerTop.width = 1008;
      }
    } else {
      // portrait
      if (config.pageSettings.pageSize === 'A4') {
        headerTop.width = 595;
      } else {
        headerTop.width = 612;
      }
    }
    return headerTop;
  }

  setUpStyle(dd, config) {
    if (config.hzHeader) {
      if (config.hzHeader.docInfo) {
        dd.styles.documentTitleStyle = {
          color: config.hzHeader.docInfo.fontColor,
          fontSize: config.hzHeader.docInfo.fontSize
        };
      }
      if (config.hzHeader.invoice) {
        dd.styles.invoice = {
          fontSize: config.hzHeader.invoice.fontSize,
          color: config.hzHeader.invoice.fontColor
        };
      }
      if (config.hzHeader.billTo) {
        dd.styles.billTo = {
          color: config.hzHeader.billTo.billToColor,
          fontSize: config.hzHeader.billTo.billTofontSize
        };
      }
      if (config.hzHeader.companyAddress) {
        dd.styles.companyAddress = {
          color: config.hzHeader.companyAddress.fontColor,
          fontSize: config.hzHeader && config.hzHeader.companyAddress.fontSize,
          margin: [0, 2, 0, 0]
        };
      }
      if (config.hzHeader.company) {
        dd.styles.company = {
          color: config.hzHeader.company.fontColor,
          fontSize: config.hzHeader.company.fontSize,
          margin: [0, 0, 0, 2]
        };
      }
      if (config.hzHeader.billTo) {
        dd.styles.billToAddress = {
          color: config.hzHeader.billTo.addressColor,
          fontSize: config.hzHeader.billTo.addressFontSize
        };
      }
    }

    if (config.itemTable) {
      dd.styles.itemRow = {
        fontSize: config.itemTable.itemRow.fontSize,
        fillColor: config.itemTable.itemRow.backgroundColor,
        color: config.itemTable.itemRow.fontColor
      };
      dd.styles.itemHeader = {
        fontSize: config.itemTable.itemHeader.fontSize,
        fillColor: config.itemTable.itemHeader.backgroundColor,
        color: config.itemTable.itemHeader.fontColor
      };
    }

    if (config.footer) {
      dd.styles.footer = {
        color: config.footer && config.footer.fontColor
      };
    }
    if (config.hospitalName) {
      dd.styles.tokenHospitalName = {
        fontSize: config.hospitalName.fontSize
      };
    }

    if (config.tokenFontSize) {
      dd.styles.token = {
        fontSize: config.tokenFontSize
      };
    }
  }

  getBucketsConfig() {
    return {
      accessKeyId: this.appConstants.buckets.opticalStore.accessKeyId,
      secretAccessKey: this.appConstants.buckets.opticalStore.secretAccessKey,
      region: this.appConstants.buckets.opticalStore.region
    };
  }

  getBucketName() {
    return {
      bucketURL: this.appConstants.buckets.opticalStore.BucketURL,
      url: this.appConstants.buckets.opticalStore.url
    };
  }

  getBarcodePrints(data) {
    console.log(data, '-=-=-=');
    const dd = {
      content: [],
      pageMargins: [
        parseInt(data[0].labelLeftMargin, 10),
        parseInt(data[0].labelTopMargin, 10),
        parseInt(data[0].labelRightMargin, 10),
        parseInt(data[0].labelBottomMargin, 10)
      ],
      // 45.5197
      pageSize: { height: 45.5197, width: 260.787 }
    };
    for (let i = 0; i < data.length; i++) {
      dd.content.push({
        table: {
          widths: ['55%', '45%'],
          body: [
            [
              {
                table: {
                  widths: ['55%', '45%'],
                  body: [
                    [
                      {
                        stack: [
                          {
                            text: data[i].itemBrandNm,
                            fontSize: 7,
                            bold: true,
                            noWrap: false
                          },
                          {
                            text: '' + data[i].brandMrp,
                            fontSize: 8,
                            bold: true,
                            noWrap: false
                          },
                          {
                            text: data[i].modalNo,
                            fontSize: 7,
                            bold: true,
                            noWrap: false,
                            margin: [0, 0, 0, 1.5]
                          },
                          {
                            // colSpan: 2,
                            text: this.organizationInfo[0].orgName,
                            fontSize: 7,
                            bold: true,
                            noWrap: true
                          }
                        ]
                      },
                      {
                        stack: [
                          {
                            image: data[i].imageObj,
                            height: parseInt(data[i].labelHeight, 10),
                            width: parseInt(data[i].labelWidth, 10),
                          },
                          {
                            text: data[i].barcode,
                            fontSize: 6,
                            bold: true,
                            alignment: 'center'
                          }
                        ]
                      }
                    ]
                    // [
                    //   {
                    //     colSpan: 2,
                    //     text: data[i].name.slice(0, 15) + (data[i].name.length > 15 ? '...' : ''),
                    //     fontSize: 4,
                    //     bold: true,
                    //     noWrap: true,
                    //   },
                    //   ''
                    // ]
                  ]
                },
                layout: {
                  hLineWidth: () => 0,
                  vLineWidth: () => 0,
                  paddingTop: () => 2,
                  paddingBottom: () => 2
                }
              }
            ]
          ]
        },
        layout: 'noBorders'
      });
    }
    this.pdfmake.createPdf(dd).print();
  }

  ngOnDestroy() { }
}
