  viewMethod() {
    this.viewRxData = !this.viewRxData;
  }
  zeroPower() {
    this.tableData = false;
    this.viewRxData = false;
  }
  withPower() {
    this.tableData = true;
    this.viewRxData = true;
  }
  fetchOpticalDetails() {
    this.framesList = [];
    this.lensList = [];
    this.contactLens = [];
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      opticalOrderNo: this.opticalOrderNo
    };
    this.opticalService.fetchPendingOrders(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.fetchOpticalDetailsList = response.data;
          if (
            this.fetchOpticalDetailsList &&
            this.fetchOpticalDetailsList.length
          ) {
            this.fetchOpticalDetailsList.forEach(element1 => {
              if (
                element1.insertOpticalOrdersFrameAndContactLensItemsReqAndRes
              ) {
                element1.insertOpticalOrdersFrameAndContactLensItemsReqAndRes.forEach(
                  element => {
                    if (element.itemTypeId && element.itemTypeId === 234) {
                      this.framesList.push(element);
                    } else if (
                      element.itemTypeId &&
                      element.itemTypeId === 235
                    ) {
                      this.contactLens.push(element);
                    }
                  }
                );
              }
            });
            this.fetchOpticalDetailsList.forEach(element1 => {
              this.lensList = this.lensList.concat(
                element1.insertOpticalOrdersLensItemReqAndRes

              );
              console.log(element1.insertOpticalOrdersLensItemReqAndRes);

            });
            if (this.lensList.length > 0) {
              this.withPowerList = [];
              this.zeroPowerList = [] ;
              this.lensList.forEach((obj) => {
                if (obj.isHavingPower === true) {
                  this.withPowerList.push(obj);
                } else  {
                  this.zeroPowerList.push(obj);
                }

              });
            }
          }
        } else if (response.responseCode === 'E400') {
          this.toaster.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }


