  // calculating costPrice used for inventory


  // used for calculating the discount percentage from the discount amount
  calculateDiscountPercent() {
    const brMrp  = this.addFramesForm.get('brandMrp').value;
    const  discountAmt = this.addFramesForm.get('discountAmt').value;
     this.costPrice = this.addFramesForm.get('costPrice').value ;
     this.invoiceQty = this.addFramesForm.get('invoiceQty').value ;
    if (
      discountAmt >
      (this.costPrice * this.invoiceQty)
    ) {
      this.toaster.warning(
        'Failure!',
        'Discount Amount should not be greater than costPrice'
      );
      return false;
    } else {
    this.discountpercent = 0;
     this.discountpercent =
      (1 / this.invoiceQty) *
      (discountAmt / this.costPrice) *
      100;
      this.discountpercent = this.discountpercent.toFixed(2) / 1;
      this.addFramesForm.patchValue({discountPercntg: this.discountpercent});
      this.changeAmount();
      // const markVal = Number(brMrp) - Number(this.costPrice);
      // this.addFramesForm.get('markUp').patchValue(markVal);

    }
    // this.calculateMarkUp();

  }
  // used for calculating the discount amount from the discount percentage
  calculateDiscountAmount() {
    const brMrp  = this.addFramesForm.get('brandMrp').value;

     this.invoiceQty = this.addFramesForm.get('invoiceQty').value ;
    const  discountPercntg = this.addFramesForm.get('discountPercntg').value;
     this.costPrice = this.addFramesForm.get('costPrice').value ;
    this.discountAmt = 0;
    if (discountPercntg > 100) {
      this.toaster.warning(
        'Failure!',
        'Discount percent should not be greater than 100'
      );
      return false;
    } else {
   this.discountAmt =
      (this.invoiceQty)  *
      (discountPercntg * this.costPrice) /
      100;
   this.discountAmt = this.discountAmt.toFixed(2) / 1;
   this.addFramesForm.patchValue({discountAmt: this.discountAmt});
   this.changeAmount();
  //  const markVal = Number(brMrp) - Number(this.costPrice);
  //     this.addFramesForm.get('markUp').patchValue(markVal);
    }
    // this.calculateMarkUp();

  }
