  fetchVendorsList() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.opticalService.fetchVendorsList(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.vendorsList = response.data;
        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      },
      () => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  fetchBrandNames() {
    this.brandNameList$ = concat(
      of([]), // default items
      this.brandNameListinput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        filter(checkStringCount => {
          if (checkStringCount.length > 1) {
            return true;
          }
        }),
        switchMap((term: string) => {
          const payload = {
            createdUsrId: this.sessionData.regId,
            orgGrpId: this.sessionData.orgGroupId,
            orgId: this.sessionData.orgId,
            itemBrandNm: term,
            manufacturerNm: this.selectedManfacNm
          };
          if (this.menuType === 'contactLenses') {
            payload['itemTypeId'] = 235;
          } else {
            payload['itemTypeId'] = 234;
          }
          return this.opticalService.fetchBrandsOpticalPos<any>(payload).pipe(
            map((response) => {
              if (response.responseCode === 'E200') {
                return response.data;
              } else if (response.responseCode === 'E400') {
                // return (this.manufacturersList = []);
              }
            }),
            catchError(() => {
              return of([]);
            })
          );
        })
      )
    );
  }

