  // api for frame data

  fetchFrameData<T>(opticalType: 'Frame Size' | 'Frame Type' | 'Frame Color' | 'Frame Shape' | 'Frame Material'
  | 'Coating Type' | 'Coating' | 'Index' | 'Lens Type', opticalCode?: string) {
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      opticalType,
      opticalCode
    };
    return this.opticalService.opticalMasterData<T>(payload).pipe(
      map((response) => {
        if (response.responseCode === 'E200') {
          return response.data;
        } else if (response.responseCode === 'E400') {
          return of([]);
        }
      }),
      catchError(() => {
        return of([]);
      })
    );
  }

