import { Action } from '@ngrx/store';
import { Employee, TestOrder } from '@app/modules/imaging/models';

export enum OpticalActionTypes {
  fetchPatientOptical = '[Optical] fetchPatientOptical',
  fetchPatientOpticalSuccess = '[Optical] fetchPatientOpticalSuccess',
  fetchPatientOpticalError = '[Optical] fetchPatientOpticalError',
  fetchOpticalItemTypes = '[Optical] fetchOpticalItemTypes',
  fetchOpticalItemTypesSuccess = '[Optical] fetchOpticalItemTypesSuccess',
  fetchOpticalItemTypesError = '[Optical] fetchOpticalItemTypesError',
  fetchOpticalVendorSuccess = '[Optical] fetchOpticalVendorSuccess',
  fetchOpticalVendors = '[Optical] fetchOpticalVendors',
  fetchOpticalInventorySuccess = '[Optical] fetchOpticalInventorySuccess',
  fetchOpticalInventory = '[Optical] fetchOpticalInventory'
}

export class FetchPatient implements Action {
  readonly type = OpticalActionTypes.fetchPatientOptical;
  constructor(public payload: any) {}
}

export class FetchPatientSuccess implements Action {
  readonly type = OpticalActionTypes.fetchPatientOpticalSuccess;
  constructor(public payload: any) {}
}

export class FetchPatientError implements Action {
  readonly type = OpticalActionTypes.fetchPatientOpticalError;
  constructor(public payload: any) {}
}

export class FetchOpticalItemTypes implements Action {
  readonly type = OpticalActionTypes.fetchOpticalItemTypes;
  constructor(public payload: any) {}
}

export class FetchOpticalItemTypesSuccess implements Action {
  readonly type = OpticalActionTypes.fetchOpticalItemTypesSuccess;
  constructor(public payload: any) {}
}

export class FetchOpticalItemTypesError implements Action {
  readonly type = OpticalActionTypes.fetchOpticalItemTypesError;
  constructor(public payload: any) {}
}

export class FetchOpticalVendorSuccess implements Action {
  readonly type = OpticalActionTypes.fetchOpticalVendorSuccess;
  constructor(public payload: any) {}
}

export class FetchOpticalVendors implements Action {
  readonly type = OpticalActionTypes.fetchOpticalVendors;
  constructor(public payload: any) {}
}

export class FetchOpticalInventorySuccess implements Action {
  readonly type = OpticalActionTypes.fetchOpticalInventorySuccess;
  constructor(public payload: any) {}
}

export class FetchOpticalInventory implements Action {
  readonly type = OpticalActionTypes.fetchOpticalInventory;
  constructor(public payload: any) {}
}

export type OpticalActions =
  |FetchPatient
  |FetchPatientSuccess
  |FetchPatientError
  |FetchOpticalItemTypes
  |FetchOpticalItemTypesSuccess
  |FetchOpticalItemTypesError
  |FetchOpticalVendorSuccess
  |FetchOpticalVendors
  |FetchOpticalInventorySuccess
  |FetchOpticalInventory;

