import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { MatDatepicker } from '@angular/material/datepicker';
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl
} from '@angular/forms';
import { Observable, concat, of, Subject } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  switchMap,
  map,
  catchError
} from 'rxjs/operators';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DatePipe } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { OpticalStoreService } from '@app/modules/optical-store/opticalStore.service';
import { OpticalStoreApiService, UtilService, CLONEDEEP, MOMENT } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { GridOptions, GridApi, ColumnApi, ColDef } from 'ag-grid/main';
import { FrameLensesModalComponent } from './frameLensesVendorInvoice.modal.component';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
// @AutoUnsubscribe()
@Component({
  selector: 'app-new-invoice',
  templateUrl: './new-invoice.component.html',
  styleUrls: ['./new-invoice.component.scss']
})
export class NewInvoiceComponent implements OnInit, OnDestroy {
  vendorNewInvoiceForm: FormGroup;
  inventoryType = 'frames';
  private sessionData: UserSession;
  public modeTransport: any[] = [];
  vendorsList: any[] = [];
  index = 0;
  brandNameList$: Observable<any>;
  brandNameListinput$ = new Subject<string>();
  public vendorNm;
  private gridApi: GridApi;
  private gridColumnApi: ColumnApi;
  vendorDataObj: any;
  defaultColDef: any;
  vendorArry: any[] = [];
  selectedRows: any[] = [];
  totalAmountWithoutTax: any;
  totalCgstAmt: any;
  totalSgstAmt: any;
  finalAmt: any;
  getTotalAmountWithoutTax: any;
  getTotalCgstAmt: any;
  getTotalSgstAmt: any;
  getFinalAmt: any;
  vendorSelectData: any;
  itemId: any;
  itemNm: any;
  components;
  discountAmt;
  discountPercntg;
  vendorData = {};
  columnDefsFrames: Array<ColDef>;
  columnDefsContactLenses: Array<ColDef>;
  columnDefsLenses: Array<ColDef>;
  frameLensData = {
    frames: [],
    contactLenses: [],
    lenses: []
  };

  rowData: any;
  dateMax = new Date();
  dateMin: any;
  orderedDate: any;
  minDate: any;
  constructor(
    @Inject(MOMENT) private moment,
    private formbuilder: FormBuilder,
    private componentOpticalService: OpticalStoreService,
    private opticalService: OpticalStoreApiService,
    private toaster: ToastrService,
    private datePipe: DatePipe,
    public dialog: MatDialog,
    @Inject(CLONEDEEP) private cloner,
    private utilService: UtilService,
    userService: UserService
  ) {
    this.sessionData = userService.getUser();
  }


  initializeGridColDefns() {
    this.columnDefsContactLenses = [
      { headerName: 'Brand Name', field: 'itemBrandNm', checkboxSelection: true },
      { headerName: 'Vendor Name', field: 'vendorNm' },
      { headerName: 'Model Number', field: 'modelNo' },
      { headerName: 'Serial Number', field: 'serialNo' },
      // { headerName: 'Description', field: 'description' },
      { headerName: 'Quantity', field: 'invoiceQty' },
      { headerName: 'Brand MRP', field: 'brandMrp' },
      { headerName: 'Cost Price', field: 'costPrice' },
      { headerName: 'Mark Up', field: 'markUp', editable: false },
      { headerName: 'HSN', field: 'hsnCode', editable: false },
      { headerName: 'Tax(%)', field: 'taxPercntg' },
      { headerName: 'Discount Amount', field: 'discountAmt'},
      { headerName: 'Discount Percent', field: 'discountPercntg'},
    ];

    this.columnDefsFrames = [
      ...this.cloner(this.columnDefsContactLenses),
      { headerName: 'Frame Type', field: 'frameTypeNm' },
      { headerName: 'Frame Size', field: 'frameSize' },
      { headerName: 'Frame Shape', field: 'frameShape' },
      { headerName: 'Frame Color', field: 'opticalColorNm' },
      { headerName: 'Frame Material', field: 'opticalMaterialNm' },
      { headerName: 'Manufacture Date', field: 'manufacturingDt',
      valueFormatter: this.agGridDateFormatter.bind(this)
     }
    ];

    this.columnDefsLenses = [
      { headerName: 'Brand Name', field: 'itemBrandNm', checkboxSelection: true },
      { headerName: 'Vendor Name', field: 'vendorNm'},
      { headerName: 'Lens Type', field: 'lensType'},
      { headerName: 'Coating Type', field: 'coatingType'},
      { headerName: 'Coating', field: 'lensCoating'},
      { headerName: 'Index', field: 'lensIndex'},
      { headerName: 'Quantity', field: 'invoiceQty' },
      { headerName: 'Brand MRP', field: 'brandMrp' },
      { headerName: 'Cost Price', field: 'costPrice' },
      { headerName: 'Discount Amount', field: 'discountAmt'},
      { headerName: 'Discount Percent', field: 'discountPercntg'},
      { headerName: 'Mark Up', field: 'markUp', editable: false },
      { headerName: 'HSN', field: 'hsnCode', editable: false },
      { headerName: 'Tax(%)', field: 'taxPercntg' }

    ];
  }

  ngOnInit() {
    this.initializeGridColDefns();
    this.modeOfTransport();
    this.init();
    this.fetchVendorsList();
    this.fetchBrandNames();
    this.selectedRows = [];
    this.changeDateFunction();
  }
  agGridDateFormatter(params) {
    console.log(params);
    if (params.value) {
      return this.moment(params.value).format('DD/MM/YYYY');
    }
  }

  init() {
    this.vendorNewInvoiceForm = this.formbuilder.group({
      vendor: this.formbuilder.control(null, Validators.required),
      invoiceNo: this.formbuilder.control('', Validators.required),
      orderedDate: this.formbuilder.control('', Validators.required),
      deliveredDate: this.formbuilder.control('', Validators.required),
      transportName: this.formbuilder.control(''),
      brandName: this.formbuilder.control('')
    });
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
    // if (this.vendorNewInvoiceForm.get('deliveredDate').value) {
    //   this.dateMax = this.vendorNewInvoiceForm.get('deliveredDate').value;
    // }
    // if (this.vendorNewInvoiceForm.get('orderedDate').value) {
    //   this.dateMin = this.vendorNewInvoiceForm.get('orderedDate').value;
    // }
  }

  changeDateFunction() {
    this.orderedDate = this.vendorNewInvoiceForm.value.orderedDate;
    this.minDate = this.orderedDate;
    return this.orderedDate;
   }
  addVendorInvoice(typeOfVendor: String): void {
    const vendorObj = this.vendorNewInvoiceForm.value.vendor;
    const typeCheckObj = {};
    typeCheckObj['type'] = typeOfVendor;
    typeCheckObj['typeCheck'] = 'add';
    typeCheckObj['vendorNm'] = vendorObj.vendorNm;
    typeCheckObj['vendorData'] = this.vendorSelectData;
    const dialogRef = this.dialog.open(FrameLensesModalComponent, {
      // height: '350px'
      data: typeCheckObj,
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.selectedRows = [];
        const dcostPrice = ((result.invoiceQty * result.costPrice) - result.discountAmt) / result.invoiceQty;
        const payload = {
          ...result,
          vendorNm: vendorObj.vendorNm,
          itemBrandNm: result.brandName.itemBrandNm || result.brandName,
          manufacturerNm: result.manufacturer.manufacturerNm,
          manufacturerId: result.manufacturer.manufacturerId,
          totalCostPrice: result.costPrice,
          costPrice: dcostPrice,
          markUp: result.brandMrp - dcostPrice,
          $$key: `${this.utilService.generateUniqueId()}-${this.index++}`
        };
        delete payload.manufacturer;
        // delete payload.costPrice;
        this.frameLensData[this.inventoryType].push(payload);
        this.gridApi.setRowData(this.frameLensData[this.inventoryType]);
      }
    });
  }

  editVendorInvoice(typeOfVendor: String): void {
    const vendorObj = this.vendorNewInvoiceForm.value.vendor;
    this.selectedRows[0].manufacturer = this.selectedRows[0].manufacturerNm;
    this.selectedRows[0].manufactureId = this.selectedRows[0].manufacturerId;
    const typeCheckObj = {};
    typeCheckObj['type'] = typeOfVendor;
    typeCheckObj['typeCheck'] = 'edit';
    typeCheckObj['selectData'] = this.selectedRows[0];
    typeCheckObj['vendorData'] = vendorObj;
    const dialogRef = this.dialog.open(FrameLensesModalComponent, {
      // height: '350px' manufacturer
      data: typeCheckObj,
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        console.log(result);
        this.selectedRows = [];
        let   dcostPrice = result.costPrice ;
        if (result.isEditAmount) {
        dcostPrice = ((result.invoiceQty * result.costPrice) - result.discountAmt) / result.invoiceQty;
        }
      const payload = {
          ...result,
          vendorNm: vendorObj.vendorNm,
          itemBrandNm: result.brandName.itemBrandNm || result.brandName,
          manufacturerNm: result.manufacturer,
          manufacturerId: result.manufactureId,
          totalCostPrice: result.costPrice,
          costPrice: dcostPrice,
          markUp: result.brandMrp - dcostPrice,
        };
        delete payload.manufacturer;
        let index = -1, selectedNodeIndex = -1;
        this.gridApi.forEachNode((val) => {
          index += 1;
          if (val.isSelected()) {
            selectedNodeIndex = index;
          }
        });
        const [selectedRowNode] = this.gridApi.getSelectedNodes();
        selectedRowNode.setData(payload);
        if (selectedNodeIndex !== -1 &&
          this.frameLensData[this.inventoryType] && this.frameLensData[this.inventoryType][selectedNodeIndex]) {
            this.frameLensData[this.inventoryType][selectedNodeIndex] = payload;
        }
        // this.frameLensData[this.inventoryType].push(payload);
        // this.selectedRows[0].setData(payload);
        // this.gridApi.setRowData(this.frameLensData[this.inventoryType]);
        // this.totalAmountCalculation();
        this.selectedRows = [];
        this.gridApi.deselectAll();
      }
    });
  }

  totalAmountCalculation() {
    this.vendorArry = [];
    this.gridApi.forEachNode(({ data }) => {
      this.vendorArry.push(data);
    });
    const getTotalcostPrice = this.vendorArry.reduce(
      (acc, val) => (acc += val.costPrice * val.invoiceQty),
      0
    );
    const totalServiceAmt = getTotalcostPrice;
    const totaltTaxAmt = this.vendorArry.reduce(
      (acc, val) =>
        (acc += (val.costPrice * val.invoiceQty * val.taxPercntg) / 100),
      0
    );
    const totalAmt = getTotalcostPrice;
    this.getTotalAmountWithoutTax = getTotalcostPrice;
    this.getTotalCgstAmt = totaltTaxAmt / 2;
    this.getTotalSgstAmt = totaltTaxAmt / 2;
    this.getFinalAmt = totalAmt + totaltTaxAmt - this.discountAmt;
  }

  // deleteVendorInvoice(data) {
  //   const selected = this.gridApi.getSelectedRows();
  //   this.gridApi.updateRowData({ remove: selected });
  //   const [rowToRemove] = selected;
  //   const index = this.frameLensData[this.inventoryType].findIndex(
  //     val => val.$$key === rowToRemove.$$key
  //   );
  //   this.frameLensData[this.inventoryType].splice(index, 1);
  // }


  deleteVendorInvoice(data) {
    const selected = this.gridApi.getSelectedRows();
    if (data) {
      const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Are you sure want to delete ?'
        },
        disableClose: true
      });

      dialogRef.afterClosed().subscribe(result => {
        if (result === true) {
        this.gridApi.updateRowData({ remove: selected });
        const [rowToRemove] = selected;
        const index = this.frameLensData[this.inventoryType].findIndex(
          val => val.$$key === rowToRemove.$$key
        );
        this.frameLensData[this.inventoryType].splice(index, 1);
    this.toaster.success('Success', 'Deleted Successfully');
      }
      });
    }
    // this.toaster.success('Success', 'Deleted Successfully');
  }


  fetchVendorsList() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.opticalService.fetchVendorsList(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.vendorsList = response.data;
        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  fetchBrandNames() {
    this.brandNameList$ = concat(
      of([]), // default items
      this.brandNameListinput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        filter(checkStringCount => {
          if (checkStringCount.length > 1) {
            return true;
          }
        }),
        //  tap(() => this.people3Loading = true),
        switchMap((term: string) => {
          const payload = {
            createdUsrId: this.sessionData.regId,
            itemTypeId: 234,
            orgGrpId: this.sessionData.orgGroupId,
            orgId: this.sessionData.orgId,
            itemBrandNm: term
          };
          return this.opticalService.fetchInventoryList(payload).pipe(
            map((response: any) => {
              if (response.responseCode === 'E200') {
                return response.data;
              } else if (response.response === 'E400') {
                // return (this.manufacturersList = []);
              }
            }),
            catchError(error => {
              return of([]);
            })
          );
        })
      )
    );
  }
  modeOfTransport() {
    this.modeTransport = ['Roadways', 'Railways', 'Airways', 'Waterways'];
  }

  onAddVendor(selectedVendor) {
    this.vendorSelectData = selectedVendor;
  }
  onAddBrand(brand) {
    this.vendorNewInvoiceForm.patchValue(brand);
  }
  onSelectionChanged(ev) {
    this.selectedRows = this.gridApi.getSelectedRows();
  }

  gridReady(params) {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    this.gridApi.setRowData([]);
  }
  // onGridReady(params) {
  //   this.gridOneApi = params.api;
  //   this.gridOneColumnApi = params.columnApi;
  // }

  addVendorNewInvoiceForm() {
    const vendorObj = this.vendorNewInvoiceForm.value.vendor;
    this.vendorData['vendorInvoiceDt'] = this.datePipe.transform(
      this.vendorNewInvoiceForm.value.deliveredDate,
      'yyyy-MM-dd'
    );
    this.vendorData['orderedDt'] = this.datePipe.transform(
      this.vendorNewInvoiceForm.value.orderedDate,
      'yyyy-MM-dd'
    );
    this.vendorData['barcode'] = this.vendorNewInvoiceForm.get(
      'invoiceNo'
    ).value;
    this.vendorData['deliveryDt'] = this.datePipe.transform(
      this.vendorNewInvoiceForm.value.deliveredDate,
      'yyyy-MM-dd'
    );
    this.vendorData['vendorInvoiceNo'] = this.vendorNewInvoiceForm.get(
      'invoiceNo'
    ).value;
    this.vendorData['shippingType'] = this.vendorNewInvoiceForm.get(
      'transportName'
    ).value;
    this.vendorData['vendorId'] = vendorObj.vendorId;
    this.vendorData['vendorNm'] = vendorObj.vendorNm;
  }

  addToInventory() {

    if (this.vendorNewInvoiceForm.invalid) {
      this.toaster.warning('Please fill all the mandatory fields');
      return;
    }

    this.addVendorNewInvoiceForm();
    this.vendorArry = [];

    const { frames, contactLenses, lenses } = this.cloner(this.frameLensData);

    frames.forEach((val) => {
      val.itemType = 'FRAME';
    });

    contactLenses.forEach((val) => {
      val.itemType = 'CONTACTLENS';
    });

    lenses.forEach((val) => {
      val.itemType = 'LENS';
    });

    this.vendorArry = [...frames, ...contactLenses, ...lenses];

    if (!this.vendorArry.length) {
      this.toaster.warning('Please add at least one item');
      return;
    }

    const totalcostPrice = this.vendorArry.reduce(
      (acc, val) => (acc += val.costPrice * val.invoiceQty),
      0
    );

    const totaltTaxAmt = this.vendorArry.reduce(
      (acc, val) =>
        (acc += (val.costPrice * val.invoiceQty * val.taxPercntg) / 100),
      0
    );
    this.discountAmt  = this.vendorArry.reduce(
      (acc, val) =>
        (acc += val.discountAmt),
      0
    );
    this.discountPercntg = this.vendorArry.reduce(
      (acc, val) =>
        (acc += val.discountPercntg),
      0
    );
    const totalServiceAmt = totalcostPrice - totaltTaxAmt;
    const totalAmt = totalcostPrice;
    this.totalAmountWithoutTax = totalcostPrice;
    this.totalCgstAmt = totaltTaxAmt / 2;
    this.totalSgstAmt = totaltTaxAmt / 2;
    this.finalAmt = totalAmt + totaltTaxAmt - this.discountAmt;
    if (this.inventoryType === 'contactLenses') {
      this.itemId = 235;
      this.itemNm = 'Contact Lense';
    } else if (this.inventoryType === 'frames') {
      this.itemId = 234;
      this.itemNm = 'Frame';
    } else if (this.inventoryType === 'lenses') {
      this.itemId = 101;
      this.itemNm = 'Lense';
    }
    this.vendorArry = this.vendorArry.map(el => {
      const taxObject = Object.assign({}, el);
      taxObject['cgstTaxPercntg'] = el.taxPercntg / 2;
      taxObject['sgstTaxPercntg'] = el.taxPercntg / 2;
      taxObject['sgstTaxAmt'] =
        (el.costPrice * el.invoiceQty * el.taxPercntg) / 100 / 2;
      taxObject['cgstTaxAmt'] =
        (el.costPrice * el.invoiceQty * el.taxPercntg) / 100 / 2;
      taxObject['taxAmt'] =
        (el.costPrice * el.invoiceQty * el.taxPercntg) / 100;
      taxObject['markUp'] = el.brandMrp - el.costPrice;
      taxObject['rowTotalAmt'] = el.costPrice * el.invoiceQty;
      taxObject['itemTypeId'] =  el.itemType === 'FRAME' ?  234 : (el.itemType === 'CONTACTLENS' ? 235 : 101) ;
      taxObject['categoryType'] = taxObject['itemTypeNm'] = el.itemType === 'FRAME' ? 'Frame' : (el.itemType === 'CONTACTLENS' ?
       'Contact Lense' : 'Lense');
      taxObject['createdUsrId'] = this.sessionData.regId;
      taxObject['orgGrpId'] = this.sessionData.orgGroupId;
      taxObject['orgId'] = this.sessionData.orgId;
      const { itemType, $$key, ...rest } = taxObject;
      return rest;
    });

    // const totalAmt = totalcostPrice + taxAmt;
    // below is the final object which is sent into insert api
    const obj = <any>{
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      taxAmt: totaltTaxAmt,
      totalAmt: totalAmt,
      totalDueAmt: totalAmt,
      totalCgstTaxAmt: this.totalCgstAmt,
      totalSgstTaxAmt: this.totalSgstAmt,
      totalServiceAmt: totalServiceAmt,
      discountAmt: this.discountAmt,
      discountPercntg : this.discountPercntg,
      ...this.vendorData
    };
    // const vendorInvoiceItemReqList = this.vendorArry.map(vendor => {
    //   const { brandName, ...rest } = this.cloner(vendor);
    //   return {
    //     ...rest,
    //     ...(brandName.opticalItemId ? { opticalItemId: brandName.opticalItemId } : { opticalItemId: null }),
    //     ...{
    //       insertOpticalItemReq: [
    //         {
    //           ...rest,
    //           ...(brandName.opticalItemId ? { opticalItemId: brandName.opticalItemId } : { opticalItemId: null }),
    //           ...(rest.vendorInvoiceItemReqList
    //             ? {
    //                 insertLensInInventory: {
    //                   ...rest.vendorInvoiceItemReqList
    //                 }
    //               } : {})
    //         }
    //       ]
    //     }
    //   };
    // });
    const vendorInvoiceItemReqList = this.vendorArry.map(vendor => {
      const { brandName, ...rest } = this.cloner(vendor);
      return {
        ...rest,
        ...(rest.itemTypeId === 101 ? {reqForLens: {
          ...rest,
          // totalCostPrice : null
        }
       } :
        {
          reqForFramesAndContactLens: {
            ...rest,
            // totalCostPrice : null,
          ...(rest.insertLensInInventory
              ? {
                insertContactLensSaleOrOrder: {
                  ...rest.insertLensInInventory
                }
              } : {})
             }
           })
      };
    });
    obj.vendorInvoiceItemReqList = vendorInvoiceItemReqList;


    this.opticalService.insertOpticalVendorInvoice(obj).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.frameLensData[this.inventoryType] = [];
          this.frameLensData = {
            frames: [],
            contactLenses: [],
            lenses: []
          };
          this.gridApi.setRowData([]);
          this.toaster.success('Success', 'Insert Successfully');
          this.vendorNewInvoiceForm.reset();
        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toaster.error('Failure!', 'vendor invoice number combination already available');
      }
    );
  }

  onFrameTypeChange() {
    this.gridApi.setRowData([]);
    if (this.inventoryType === 'frames') {
      this.gridApi.setColumnDefs(this.columnDefsFrames);
    } else if (this.inventoryType === 'contactLenses') {
      this.gridApi.setColumnDefs(this.columnDefsContactLenses);
    } else if (this.inventoryType === 'lenses') {
       this.gridApi.setColumnDefs(this.columnDefsLenses);
     }
    this.gridApi.setRowData(this.frameLensData[this.inventoryType]);
  }

  getMethod() {}
  ngOnDestroy() {
  }
}
