import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { MatDatepicker } from '@angular/material';
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl
} from '@angular/forms';
import { Observable, concat, of, Subject } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  switchMap,
  map,
  catchError
} from 'rxjs/operators';
import { DatePipe } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { OpticalStoreService } from '@app/modules/optical-store/opticalStore.service';
import { OpticalStoreApiService, MOMENT } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { GridOptions, GridApi, ColumnApi } from 'ag-grid/main';
import { VendorInvoiceBillComponent } from '../../vendor-invoice-bill-dialog/vendor-invoice-bill-dialog.component';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
// @AutoUnsubscribe()
@Component({
  selector: 'app-invoice-history',
  templateUrl: './invoice-history.component.html',
  styleUrls: ['./invoice-history.component.scss']
})
export class InvoiceHistoryComponent implements OnInit, OnDestroy {
  invoiceHistoryForm: FormGroup;
  private sessionData: UserSession;
  modeTransport: any[] = [];
  vendorsList: any[] = [];
  fetchVendorArry: any[] = [];
  brandNameList$: Observable<any>;
  brandNameListinput$ = new Subject<string>();
  public vendorNm;
  private gridApi: GridApi;
  dateMax = new Date();
  dateMin: any;
  fromDt: any;
 minDate: any;
  private gridColumnApi: ColumnApi;
  gridOptions: GridOptions = {
    columnDefs: [
      { headerName: 'Invoice Number', field: 'vendorInvoiceNo' },
      { headerName: 'Vendor Name', field: 'vendorNm' },
      { headerName: 'Invoice Date', field: 'vendorInvoiceDt', valueFormatter : this.agGridDateFormatter.bind(this) },
      { headerName: 'Total Amount', field: 'totalAmt' },
      { headerName: 'Paid Amount', field: 'paidAmt' },
      { headerName: 'Due Amount', field: 'totalDueAmt' },
      { headerName: 'Mode of Transport', field: 'shippingType' },
      {
        headerName: 'Pay Bill',
        suppressFilter: true,
        field: 'payNow',
        cellRendererFramework: VendorInvoiceBillComponent
      }
    ],
    context: {
      componentParent: this
    }
  };

  rowData: any;

  constructor(
    @Inject(MOMENT) private moment,
    private formbuilder: FormBuilder,
    private componentOpticalService: OpticalStoreService,
    private opticalService: OpticalStoreApiService,
    private toaster: ToastrService,
    private datePipe: DatePipe,
    userService: UserService,
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.init();
    this.fetchVendorsList();
    this.fetchInvoiceHistoryForm();
    this.changeDateFunction();
  }

  init() {
    this.invoiceHistoryForm = this.formbuilder.group({
      vendor: this.formbuilder.control(null),
      vendorInvoiceNo: this.formbuilder.control(''),
      fromDt: this.formbuilder.control(''),
      toDt: this.formbuilder.control('')
    });
    const currentDate = this.datePipe.transform(new Date(), 'yyyy-MM-dd');
    this.invoiceHistoryForm.controls['fromDt'].setValue(currentDate);
    this.invoiceHistoryForm.controls['toDt'].setValue(currentDate);
  }

  gridReady(params) {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
  }
  agGridDateFormatter(params) {
    return this.moment(params.value).format('DD/MM/YYYY');
  }

  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }

  changeDateFunction() {
   this.fromDt = this.invoiceHistoryForm.value.fromDt;
   this.minDate = this.fromDt;
   return this.fromDt;
  }


  fetchVendorsList() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.opticalService.fetchVendorsList(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.vendorsList = response.data;
          // this.invoiceHistoryForm.reset();
        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  onAddVendor(selectedVendor) {
    console.log(selectedVendor);
  }

  fetchInvoiceHistoryForm() {
    const payload = {
      fromDt: this.datePipe.transform(
        this.invoiceHistoryForm.value.fromDt,
        'yyyy-MM-dd'
      ),
      toDt: this.datePipe.transform(
        this.invoiceHistoryForm.value.toDt,
        'yyyy-MM-dd'
      ),
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    if (this.invoiceHistoryForm.get('vendorInvoiceNo').value) {
      payload['vendorInvoiceNo'] = this.invoiceHistoryForm.get(
        'vendorInvoiceNo'
      ).value;
    }
    this.opticalService.fetchVendorInvoiceDetails(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.fetchVendorArry = response.data.map(function(el) {
            const priceObject = Object.assign({}, el);
            priceObject['paidAmt'] = el.totalAmt - el.totalDueAmt;
            priceObject['payNow'] = 'payNow';
            return priceObject;
          });
          this.gridApi.setRowData(this.fetchVendorArry);
        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }
  ngOnDestroy() {
  }
}
