import { Component, OnInit, ChangeDetectorRef, OnDestroy } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  OpticalStoreApiService,
  LoginApiService,
  UtilService
} from '@core/services';
import { OpticalStoreService } from '@app/modules/optical-store/opticalStore.service';
import { Store, select } from '@ngrx/store';
import { ToastrService } from 'ngx-toastr';
import { MatDatepicker } from '@angular/material';
import { GetAgePipe } from '@shared/pipes/getAgePipe';
import { FormGroup, FormBuilder, Validators, NgForm } from '@angular/forms';
import { Observable, Subscription } from 'rxjs';
import { getSearchPatient } from '@app/modules/optical-store/reducers';
import { DatePipe } from '@angular/common';
import { FetchPatientSuccess } from '@app/modules/optical-store/actions';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { ActivatedRoute, Params } from '@angular/router';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';

// @AutoUnsubscribe()
@Component({
  selector: 'app-optical-point-of-sale',
  templateUrl: './optical-point-of-sale.component.html',
  styleUrls: ['./optical-point-of-sale.component.scss']
})
export class OpticalPointOfSaleComponent implements OnInit, OnDestroy {
  now: Date = new Date();
  posType = 'frames';
  addedItems: any = [];
  honorificsList: any = [];
  patient: any;
  isShowOrderInfo: Boolean = false;
  private sessionData: UserSession;
  patientDetailsForm: FormGroup;
  public showBilling: Boolean = false;
  public patientData$: Observable<any>;
  public patientDataSubscription: Subscription;
  public opticalCommonDataSubscription: Subscription;
  patientAgeMonth;
  patientAgeYear;
  patientAgeDays;
  public isClearPos = false;
  public paientFormSubmitted = false;
  public orderType;
  patient_details_validation_messages = {
    firstName: [
      { type: 'required', message: '* First Name is required' },
      { type: 'minlength', message: '* First Name must be at least 3 characters long' },
      { type: 'maxlength', message: '* First Name cannot be more than 50 characters long' },
      { type: 'pattern', message: 'First Name must contain only letters' }
    ],
    middleName: [
      { type: 'minlength', message: '* Middle Name must be at least 3 characters long' },
      { type: 'maxlength', message: '* Middle Name cannot be more than 50 characters long' },
      { type: 'pattern', message: 'Middle Name must contain only letters' }
    ],
    LastName: [
      { type: 'required', message: '* Last Name is required' },
      { type: 'minlength', message: '* Last Name must be at least 1 characters long' },
      { type: 'maxlength', message: '* Last Name cannot be more than 50 characters long' },
      { type: 'pattern', message: 'Last Name must contain only letters' }
    ],
    email: [{ type: 'pattern', message: 'Enter a valid email' }],
    address: [
      { type: 'required', message: '* Address is required' },
      { type: 'minlength', message: '* Address is Too Small' },
      { type: 'maxlength', message: '* Address is Too Long' }
    ],
    pincode: [
      { type: 'minlength', message: '* Pincode is Too Small' },
      { type: 'maxlength', message: '* Pincode is Too Long' },
      { type: 'pattern', message: 'Enter Valid Pincode' }
    ],
    cityName: [
      { type: 'required', message: '* City is required' },
      { type: 'minlength', message: '* City is Too Small' },
      { type: 'maxlength', message: '* City is Too Long' },
      { type: 'pattern', message: 'Enter a Valid City' }
    ],
    CountryName: [
      { type: 'required', message: '* Country is required' },
      { type: 'minlength', message: '* Country is Too Small' },
      { type: 'maxlength', message: '* Country is Too Long' },
      { type: 'pattern', message: 'Enter a Valid Country' }
    ],
    mobileNo: [
      { type: 'minlength', message: 'Mobile No is Too Small' },
      { type: 'maxlength', message: 'Mobile No is Too Long' },
      { type: 'pattern', message: 'Enter a Valid Mobile No' }
    ],
    aadhaarNo: [
      { type: 'minlength', message: 'Aadhar No is Too Small' },
      { type: 'maxlength', message: 'Aadhar No is Too Long' },
      { type: 'pattern', message: 'Enter a Valid Aadhar No' }
    ],
    age: [
      { type: 'required', message: '* Age is required' }
    ],
    gender: [
      { type: 'required', message: '* Gender is required' }
    ]
  };
  dateMax = new Date();
  minDate = new Date(1900, 0, 1);
  paymentData: any;
  showBillingFromPending = false;
  opticalData: any;

  constructor(
    private translate: TranslateService,
    private loginService: LoginApiService,
    private activatedRoute: ActivatedRoute,
    private opticalService: OpticalStoreApiService,
    private componentOpticalStoreService: OpticalStoreService,
    private store: Store<any>,
    private toaster: ToastrService,
    private agePipe: GetAgePipe,
    private formbuilder: FormBuilder,
    private changeDecRef: ChangeDetectorRef,
    private datePipe: DatePipe,
    private utilservice: UtilService,
    private opticalStoreService: OpticalStoreService,
    userService: UserService,
  ) {
    translate.use(translate.getBrowserLang() || 'en');
    this.sessionData = userService.getUser();

    this.patientData$ = this.store.pipe(select(getSearchPatient));
  }

  ngOnInit() {
    // new sFetchPatientSuccess(data);
    this.activatedRoute.queryParams.subscribe(
      (params: Params) => {
        this.orderType = params['type'];
      }
    );
    this.componentOpticalStoreService.paymentData
      .subscribe(arg => {
        if (arg) {
          this.showBillingFromPending = true;
          this.showBilling = true;
          this.paymentData = arg;
        }
      });

    this.initForm();
    this.fetchHonorifics();
    if (this.orderType === 'order') {
      this.isShowOrderInfo = true;
    }
    this.store.dispatch(new FetchPatientSuccess([]));
    this.patientDataSubscription = this.patientData$.subscribe(
      patchPatientData => {
        const [data] = patchPatientData.data;
        this.componentOpticalStoreService.patientData.next(patchPatientData.data);
        if (patchPatientData.data) {
          this.opticalStoreService.setPatientData(patchPatientData.data);
        }
        if (!patchPatientData.errorOccured && data) {
          this.patientDetailsForm.reset();
          this.patientDetailsForm.patchValue(data);
          const age = data.birthDt
            ? this.utilservice.parseDob(data.birthDt)
            : this.utilservice.parseAgeIndays(data.age);
          const ageObj = {
            patientAgeYear: age.get('years'),
            patientAgeMonth: age.get('months'),
            patientAgeDays: age.get('days')
          };
          this.patientDetailsForm.patchValue(ageObj);
        }
      }
    );

    this.patientDetailsForm
      .get('patientAgeYear')
      .valueChanges.subscribe(val => {
        this.onChangeAge(Number(val), 'patientAgeYear');
      });

    this.patientDetailsForm
      .get('patientAgeMonth')
      .valueChanges.subscribe(val => {
        this.onChangeAge(Number(val), 'patientAgeMonth');
      });

    this.patientDetailsForm
      .get('patientAgeDays')
      .valueChanges.subscribe(val => {
        this.onChangeAge(Number(val), 'patientAgeDays');
      });

     this.opticalCommonDataSubscription = this.componentOpticalStoreService.opticalCommonData.subscribe(val => {
        if (val) {
          this.opticalData = val;
          if (this.opticalData === 'pointOfSale' || this.opticalData === 'pointOfOrder') {
            this.showBilling = false;
            this.posType = 'frames';
          }
        }
      });
  }
  initForm() {
    this.patientDetailsForm = this.formbuilder.group({
      patientId: null,
      honorificNm: this.formbuilder.control(null),
      firstNm: this.formbuilder.control(null, [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(50),
        Validators.pattern(/^[a-zA-Z ]*$/)
      ]),
      middleNm: this.formbuilder.control(null, [
        Validators.minLength(3),
        Validators.maxLength(50),
        Validators.pattern(/^[a-zA-Z ]*$/)
      ]),
      lastNm: this.formbuilder.control(null, [
        // Validators.required,
        Validators.minLength(1),
        Validators.maxLength(50),
        Validators.pattern(/^[a-zA-Z ]*$/)
      ]),
      emailId: this.formbuilder.control(
        null, [Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)]
      ),
      mobileNo: this.formbuilder.control(null, [
        Validators.minLength(10),
        Validators.maxLength(10),
        Validators.pattern(/(5|6|7|8|9)\d{9}$/)
      ]),
      aadhaarNo: this.formbuilder.control(null, [
        Validators.minLength(12),
        Validators.maxLength(12),
        Validators.pattern(/^[2-9][0-9]{11}$/)
      ]),
      hosPatientId: this.formbuilder.control(null),
      birthDt: this.formbuilder.control(null),
      age: this.formbuilder.control(''),
      patientAgeMonth: this.formbuilder.control(null),
      patientAgeYear: this.formbuilder.control(null),
      patientAgeDays: this.formbuilder.control(null),
      gender: this.formbuilder.control(''),
      addressLine1: this.formbuilder.control(null, [
        // Validators.required,
        Validators.minLength(1),
        Validators.maxLength(300)
      ]),
      pinCode: this.formbuilder.control(null, [
        Validators.minLength(6),
        Validators.maxLength(6),
        Validators.pattern(/^[1-9][0-9]{5}$/)
      ]),
      cityNm: this.formbuilder.control(null, [
        // Validators.required,
        Validators.minLength(3),
        Validators.maxLength(20),
        Validators.pattern(/^[a-zA-Z ]*$/)
      ]),
      stateNm: this.formbuilder.control(null),
      districtNm: this.formbuilder.control(null),
      countryNm: this.formbuilder.control('India', [
        // Validators.required,
        Validators.minLength(3),
        Validators.maxLength(30),
        Validators.pattern(/^[a-zA-Z ]*$/)
      ])
    });
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  dobAdded(dob) {
    const ageArray = this.utilservice.parseDob(
      this.datePipe.transform(dob.value, 'yyyy-MM-dd')
    );
    const ageObj = {
      patientAgeYear: ageArray.get('years'),
      patientAgeMonth: ageArray.get('months'),
      patientAgeDays: ageArray.get('days')
    };
    this.patientDetailsForm.patchValue(ageObj);
  }

  fetchHonorifics() {
    const payload = {
      orgId: this.sessionData.orgId,
      masterType: 'HONORIFICS',
      authenticatedUserId: this.sessionData.regId
    };

    this.loginService.fetchListOfCommonValues(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.honorificsList = response.data;
          this.changeDecRef.detectChanges();

        } else if (response.responseCode === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  insertItem(item) {
    if (this.orderType === 'order') {
      item.totalPrice = Number(item.orderQty) * Number(item.brandMrp);
    } else {
    item.totalPrice = Number(item.quantity) * Number(item.brandMrp);
    }
    item.totalTax = Number(item.cgstTaxPercntg) + Number(item.sgstTaxPercntg);
    this.addedItems.push(item);
  }

  proceddToBilling() {
    this.paientFormSubmitted = true;
    if (this.patientDetailsForm.invalid) {
      return;
    }
    if (this.addedItems.length === 0) {
      this.toaster.warning('Please Add Items');
      return;
    }
    this.posType = '';
    this.showBilling = true;
    this.componentOpticalStoreService.showsBilling.next('pointofsale');
    this.patient = { ...this.patientDetailsForm.value };
    if (this.patient.birthDt) {
      this.patient.birthDt = this.datePipe.transform(
        this.patient.birthDt,
        'yyyy-MM-dd'
      );
    } else {
      this.patient.age =
        Number(this.patient.patientAgeYear * 365) +
        Number(this.patient.patientAgeMonth * 31) +
        Number(this.patient.patientAgeDays);
    }
  }

  deleteItem(pos) {
    this.addedItems.splice(pos, 1);
  }

  // onchangeAge(val, ageType) {
  //   this[ageType] = Number(val);
  // }

  clearPos() {
    this.patientDetailsForm.reset();
    this.paientFormSubmitted = false;
    this.isClearPos = true;
    this.addedItems = [];
  }

  onChangeAge(ev, ageType) {
    if (ageType === 'patientAgeYear') {
      this.patientDetailsForm.patchValue({
        age:
          Number(ev * 365) +
          Number(this.patientDetailsForm.get('patientAgeMonth').value * 31) ||
          0 + Number(this.patientDetailsForm.get('patientAgeDays').value) ||
          0
      });
    } else if (ageType === 'patientAgeMonth') {
      this.patientDetailsForm.patchValue({
        age:
          Number(this.patientDetailsForm.get('patientAgeYear').value * 365) ||
          0 + Number(ev * 31) ||
          0 + Number(this.patientDetailsForm.get('patientAgeDays').value) ||
          0
      });
    } else if (ageType === 'patientAgeDays') {
      this.patientDetailsForm.patchValue({
        age:
          Number(this.patientDetailsForm.get('patientAgeYear').value * 365) ||
          0 +
          Number(this.patientDetailsForm.get('patientAgeMonth').value * 31) ||
          0 + Number(ev) ||
          0
      });
    }
  }

  cancelBilling(ev) {
    this.showBilling = false;
    this.addedItems = [];
    this.posType = 'frames';
    this.patientDetailsForm.reset();
    this.paientFormSubmitted = false;
  }
  ngOnDestroy() {
  }
}
