import { OpticalActions, OpticalActionTypes } from '@app/modules/optical-store/actions';

const initialState = {
  isLoading: false,
  errorOccured: false,
  data : {
    clickedOnSearch: false,
    data: []
  }
};

export function fetchOpticalVendorReducer(state = initialState, action: OpticalActions) {
  switch (action.type) {
    case OpticalActionTypes.fetchOpticalVendors:
      return {
        isLoading: true,
        errorOccured: false,
        data : {
          clickedOnSearch: false,
          data: []
        }
      };
    case OpticalActionTypes.fetchOpticalVendorSuccess:
     return {
        isLoading: false,
        errorOccured: false,
        data : action.payload
      };
    default:
      return state;
  }
}
