import { Component, OnInit, Inject } from '@angular/core';
import { AgRendererComponent, ICellRendererAngularComp } from 'ag-grid-angular';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ICellRendererParams } from 'ag-grid/dist/lib/rendering/cellRenderers/iCellRenderer';
import { RowNode } from 'ag-grid/dist/lib/entities/rowNode';
import { GridApi } from 'ag-grid/dist/lib/gridApi';
import { OpticalStoreApiService, JSBARCODE } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { OpticalStoreService } from '@app/modules/optical-store/opticalStore.service';
import { CommentsDialogComponent } from '@shared/components/comments-dialog/comments-dialog.component';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { UtilService } from '@core/services/utils.service';
import { DatePipe } from '@angular/common';
import { OpticalInvoiceListComponent } from '../optical-invoice-list.component';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
// @AutoUnsubscribe()
@Component({
  selector: 'app-pos-inventory',
  templateUrl: './optical-actions-list.component.html',
  styleUrls: ['./../optical-invoice-list.component.scss']
})
export class PosInvoiceListComponent
  implements OnInit, ICellRendererAngularComp {
  public rowNode: RowNode;
  private api: GridApi;
  private params: ICellRendererParams;
  private selectedRows: any[] = [];
  private sessionData: UserSession;
  private parentComponent: OpticalInvoiceListComponent;

  constructor(
    private componentOpticalStoreService: OpticalStoreService,
    public dialog: MatDialog,
    private opticalService: OpticalStoreApiService,
    private toastr: ToastrService,
    userService: UserService,
    @Inject(JSBARCODE) private jsbarcode,
    private toaster: ToastrService,
    private datePipe: DatePipe,
    private utilService: UtilService
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {}

  agInit(params: ICellRendererParams): void {
    this.api = params.api;
    this.rowNode = params.node;
    this.params = params;
    this.parentComponent = params.context.componentParent;
  }

  deleteSelectedRow() {
    const dialogRef = this.dialog.open(CommentsDialogComponent, {
      data: {
        title: 'Cancel Invoice',
        data: 'Provide Reason'
      },
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const deletePayload = {
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          createdUsrId: this.sessionData.regId,
          cancelUsrId: this.sessionData.regId,
          cancelReason: result,
          deleteUsrId: this.sessionData.regId,
          pointOfSaleId: this.rowNode.data.pointOfSaleId,
          totalBillDueAmt: this.rowNode.data.totalBillDueAmt,
          opticalBillNo: this.rowNode.data.opticalBillNo,
          patientId: this.rowNode.data.patientId,
          totalPaidAmt: this.rowNode.data.paidAmt
        };
        this.opticalService.deletePos(deletePayload).subscribe(
          (response: any) => {
            if (response.responseCode === 'E200') {
              this.params.context.componentParent.fetchPointOfSaleInventory();
              // this.api.removeItems([this.rowNode]);
              this.toastr.success('Success!', response.responseMessage);
            } else if (response.response === 'E400') {
              this.toastr.error('Failure!', response.responseMessage);
            }
          },
          err => {
            this.toastr.error('Failure!', 'Something went wrong');
          }
        );
      }
    });
  }

  returnSelectedRow() {
    this.params.context.componentParent.onReturnItem(this.rowNode);
  }

  refresh(): boolean {
    return false;
  }

 // print functionality ( present in parent component(optical-invoice-list.component))
  printInvoice() {
    if (this.parentComponent) {
      this.parentComponent.posPrint(this.params.data);
    }
  }
}
