import { Component, OnInit, Input, AfterViewInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDatepicker, MatSidenav, MatDialog } from '@angular/material';
import { MatSnackBar } from '@angular/material/snack-bar';
import { OpticalStoreService } from '@app/modules/optical-store/opticalStore.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { UserSession } from '@core/models/user-session';
import {
  UtilService,
  UserService,
  OpticalStoreApiService
} from '@core/services';

@Component({
  selector: 'app-lens-add-to-inventory',
  templateUrl: './lens-add-to-inventory.component.html',
  styleUrls: ['./lens-add-to-inventory.component.scss']
})
export class LensAddToInventoryComponent implements OnInit, AfterViewInit {
  @Input()
  sideNavRef: MatSidenav;
  lensToInventoryForm: FormGroup;
  vendorsList: any[] = [];
  brandNamesInvList: any[] = [];
  manufacturersList: any[] = [];
  lensInventoryItems: any;
  private sessionData: UserSession;
  opticalOrderNo: any;
  inventoryFormSubmitted = false;
  ordersList: any;
  opticalItemOrderId: any;
  manufacturer: any;
  selectedVendor: any;
  markupVal: number;
  inventory_details_validation_messages = {
    CostPrice: [
      { type: 'required', message: '* Cost Price is required' },
    ],
    Vendor: [
      { type: 'required', message: '* Vendor is required' },
    ],
    Manufacturer: [
      { type: 'required', message: '* Manufacturer is required' },
    ],
    Quantity: [
      { type: 'required', message: '* Quantity is required' },
    ]
  };
  insufficientStockKey = false;
  constructor(
    private componentOpticalService: OpticalStoreService,
    private formBuilder: FormBuilder,
    private utilService: UtilService,
    private userService: UserService,
    private router: Router,
    private snackBar: MatSnackBar,
    private opticalService: OpticalStoreApiService,
  ) {
    this.sessionData = userService.getUser();
  }

  ngAfterViewInit() {
    this.componentOpticalService.lensInvData.subscribe(res => {
      if (res) {
      this.opticalOrderNo = res.opticalOrderNo;
      this.opticalItemOrderId = res.opticalItemOrderId;

      }
      // this.fetchPendingOrdersList();
      this.fetchBrandNameList();

    });
  }
  ngOnInit() {
    this.initForm();
    this.fetchVendorsList();
  }

  initForm() {
    this.lensToInventoryForm = this.formBuilder.group({
      itemBrandNm: '',
      brandMrp: '',
      hsnCode: '',
      taxPercntg: '',
      lensCoating: '',
      lensType: '',
      coatingType: '',
      lensIndex: '',
      costPrice: this.formBuilder.control(null, Validators.required),
      receivedQty: this.formBuilder.control(null, Validators.required),
      barcode: '',
      vendor: this.formBuilder.control(null, Validators.required),
      manufacturer: this.formBuilder.control(null, Validators.required),
      brandNm: ''
    });
  }

  fetchPendingOrdersList() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      opticalOrderNo: this.opticalOrderNo
    };
    this.opticalService.fetchPendingOrders(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.ordersList = response.data[0];
        } else if (response.response === 'E400') {
          this.snackBar.open(response.responseMessage, 'error', {
            duration: 1500
          });
        }
      },
      err => {
        this.snackBar.open('Something went wrong', 'error', {
          duration: 1500
        });
      }
    );
  }
  /**
   @function fetchBrandNameList
   @description this method is to fetch the list of the lens items which are not present in inventory
   */
  fetchBrandNameList() {
    this.brandNamesInvList = [];
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      opticalItemOrderId: this.opticalItemOrderId
    };
    this.opticalService.fetchInsufficientInventoryItems(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.brandNamesInvList = response.data;
          console.log(this.brandNamesInvList);
        } else if (response.response === 'E400') {
          this.snackBar.open(response.responseMessage, 'error', {
            duration: 1500
          });
        }
      },
      err => {
        this.snackBar.open('Something went wrong', 'error', {
          duration: 1500
        });
      }
    );
  }
  /**
   @function fetchVendorsList
   @description to get vendors list who are already added
   */
  fetchVendorsList() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.opticalService.fetchVendorsList(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.vendorsList = response.data;
        } else if (response.response === 'E400') {
          this.snackBar.open(response.responseMessage, 'error', {
            duration: 1500
          });
        }
      },
      err => {
        this.snackBar.open('Something went wrong', 'error', {
          duration: 1500
        });
      }
    );
  }
  /**
   * @description this method provides the manufacture list of specific selected vendor
   * @param selectedVendor
   */

  onAddVendor(selectedVendor) {
    this.manufacturersList = selectedVendor.manufacturerList;
    this.selectedVendor = selectedVendor;
  }

  onAddManufacturer(selectedManufacturer) {
    this.manufacturer = selectedManufacturer;
  }
/**
 @function onSelectBrandName
 @description this method is used for patching the values as the brand name is selected
 */
  onSelectBrandName(event) {
  this.lensInventoryItems = this.lensToInventoryForm.get('brandNm').value;
  this.lensToInventoryForm.patchValue(this.lensInventoryItems);
  this.lensToInventoryForm.patchValue({receivedQty: this.lensInventoryItems.orderQty});
}

addToInventory() {
  this.inventoryFormSubmitted = true;
  if (this.lensToInventoryForm.valid) {
  const payload = {
    orgId: this.sessionData.orgId,
    orgGrpId: this.sessionData.orgGroupId,
    createdUsrId: this.sessionData.regId,
    opticalItemOrderId: this.opticalItemOrderId,
    opticalOrderNo: this.opticalOrderNo,
    insertLensInInventoryReq: this.lensToInventoryForm.value,
    itemOrderLensDetailedId: this.brandNamesInvList[0].itemOrderLensDetailedId,
    orderQty: this.brandNamesInvList[0].orderQty,
  };
  payload.insertLensInInventoryReq.manufacturerNm = this.manufacturer ? this.manufacturer.manufacturerNm : '';
  payload.insertLensInInventoryReq.manufacturerId = this.manufacturer ? this.manufacturer.manufacturerId : '';
  payload.insertLensInInventoryReq.vendorId = this.selectedVendor ? this.selectedVendor.vendor : '';
  payload.insertLensInInventoryReq.vendorNm = this.selectedVendor ? this.selectedVendor.vendorNm : '';
  payload.insertLensInInventoryReq.markUp = this.markupVal;
  delete payload.insertLensInInventoryReq.brandNm;
  this.opticalService.insertLensInventoryFromPendingOrder(payload).subscribe(
    (response: any) => {
      if (response.responseCode === 'E200') {
     this.lensToInventoryForm.reset();
     this.inventoryFormSubmitted = false;
     this.closeSideNav();
    //  this.fetchPendingOrdersList();
    this.router.navigate(['/opticalStore/optical-pending-orders']);

      } else if (response.response === 'E400') {
        this.snackBar.open(response.responseMessage, 'error', {
          duration: 1500
        });
      }
    },
    err => {
      this.snackBar.open('Something went wrong', 'error', {
        duration: 1500
      });
    }
  );
  }

}

/**
 @function checkCostPrice
 @description this method is to check whether the costprice is greater than MRP
 */
checkCostPrice() {
  if (
    this.lensToInventoryForm.get('brandMrp').value &&
    this.lensToInventoryForm.get('costPrice').value
  ) {
    const mrp = this.lensToInventoryForm.get('brandMrp').value;
    const cp = this.lensToInventoryForm.get('costPrice').value;
    if (cp > mrp) {
      this.snackBar.open('COST PRICE should not be greater than BRAND MRP', 'error', {
        duration: 3000
      });
      this.lensToInventoryForm.get('costPrice').patchValue('');
    } else {
      this.markupVal = Number(mrp) - Number(cp);

    }
  }
}

checkReqQuantity() {
  if (
        this.lensToInventoryForm.get('receivedQty').value
      ) {
        const recQty = this.lensToInventoryForm.get('receivedQty').value;
        const ordQty = this.brandNamesInvList[0].orderQty;
        if (recQty < ordQty) {
          this.snackBar.open('Entered Quantity is less than Ordered Quantity', 'error', {
            duration: 3000
          });
          this.lensToInventoryForm.get('receivedQty').patchValue('');
        }
      }
}
  closeSideNav() {
    this.insufficientStockKey = true;
    this.componentOpticalService.insufficientStockKey.next(this.insufficientStockKey);
    this.sideNavRef.close();
  }

}
