import { Component, OnInit, NgModule, ViewChild, OnDestroy, SimpleChanges, OnChanges, Inject } from '@angular/core';
import {
  FormControl,
  FormGroup,
  FormBuilder,
  ReactiveFormsModule,
  FormsModule,
  Validators
} from '@angular/forms';
import { GridOptions, GridApi, ColumnApi } from 'ag-grid/main';
import { NgSelectModule, NgOption } from '@ng-select/ng-select';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription, Observable, of, concat } from 'rxjs';
import {
  distinctUntilChanged,
  debounceTime,
  switchMap,
  tap,
  catchError,
  map,
  count,
  filter
} from 'rxjs/operators';
import { UserSession } from '@core/models/user-session';
import {
  Router,
  ActivatedRouteSnapshot,
  ActivatedRoute,
  NavigationExtras
} from '@angular/router';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { OpticalStoreService } from '../../../opticalStore.service';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { OpticalStoreApiService, MOMENT} from '@core/services';
import { Store, select } from '@ngrx/store';
import { getOpticalVendors } from '@app/modules/optical-store/reducers';
import { AutoUnsubscribe } from '../../../../../../../node_modules/ngx-auto-unsubscribe';
import { UserService } from '@core/services/user.service';
import { FetchOpticalInventorySuccess, FetchOpticalVendorSuccess } from '@app/modules/optical-store/actions';

@AutoUnsubscribe()
@Component({
  selector: 'app-all-optical-suppliers',
  templateUrl: './all-optical-suppliers.component.html',
  styleUrls: ['./all-optical-suppliers.component.scss']
})
export class AllOpticalSuppliersComponent implements OnInit, OnDestroy, OnChanges {
  private sessionData: UserSession;
  private gridApi: GridApi;
  private gridColumnApi: ColumnApi;
  private selectedRows: any[];
  vendorsList: any[] = [];
  clickedEdit: Boolean;
  currentSupplier: any;
  objectKeys = Object.keys;
  vendorManufacturers: any[] = [];
  menuType = 'manufacturers';
  public vendorList$: Observable<any>;
  public vendorListSubscription: Subscription;
  public backToAll = false;
  public tempVendorsList;
  columnDefs = [
    { headerName: 'Invoice Number', field: 'vendorInvoiceNo'},
    { headerName: 'Ordered Date', field: 'orderedDt', valueFormatter : this.agGridDateFormatter.bind(this) },
    { headerName: 'Invoice Date', field: 'vendorInvoiceDt', valueFormatter : this.agGridDateFormatter.bind(this)},
    {
      headerName: 'Invoice Amount',
      field: 'totalAmt',

    },
    { headerName: 'Paid Amount', field: 'paidAmt' },

  ];

  constructor(
    @Inject(MOMENT) private moment,
    private opticalService: OpticalStoreApiService,
    private componentOpticalStoreService: OpticalStoreService,
    private toaster: ToastrService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private formbuilder: FormBuilder,
    public dialog: MatDialog,
    private store: Store<any>,
    userService: UserService,
  ) {
    this.vendorList$ = this.store.pipe(select(getOpticalVendors));
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    // this.store.dispatch(new FetchOpticalInventorySuccess({clickedOnSearch: false,
    //   data: []}));
    this.selectedRows = [];
      this.fetchVendorsList();
      this.store.dispatch(new FetchOpticalVendorSuccess({clickedOnSearch: false, data: []}));
      this.vendorListSubscription = this.vendorList$.subscribe(
        vendorListData => {
            this.backToAll = vendorListData.data.clickedOnSearch ? true : false;
            this.vendorsList = vendorListData.data.data;
          if (vendorListData.data.data.length > 0) {
            this.currentSupplier = this.vendorsList[0];
            this.vendorsList[0].selected = true;
          }
        }
      );

  }

  ngOnChanges(changes: SimpleChanges) {
    // this.store.dispatch(new FetchOpticalInventorySuccess({clickedOnSearch: false, data: []}));
  }

  ngOnDestroy() {
  }
  agGridDateFormatter(params) {
    return this.moment(params.value).format('DD/MM/YYYY');
  }
  getStoreData() {
    this.vendorListSubscription = this.vendorList$.subscribe(
      vendorListData => {
          this.backToAll = vendorListData.data.clickedOnSearch ? true : false;
          this.vendorsList = vendorListData.data.data;
        if (vendorListData.data.data.length > 0) {
          this.currentSupplier = this.vendorsList[0];
          this.vendorsList[0].selected = true;
        }
        this.store.dispatch(new FetchOpticalInventorySuccess({clickedOnSearch: false, data: []}));
      }
    );
  }

  fetchVendorsList() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      isFullDetails: 'true'
    };
    this.opticalService.fetchVendorsList(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.vendorsList = response.data;
          this.tempVendorsList = response.data;
          if (this.vendorsList.length > 0) {
            this.currentSupplier = this.vendorsList[0];
            this.vendorsList[0].selected = true;
          }
        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }

      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  selectedVendor(vendor, pos) {
    this.vendorsList.forEach(function(element) {
     if (element.vendorId === vendor.vendorId) {
      element.selected = true;
     } else {
      element.selected = false;
     }
    });
    this.currentSupplier = vendor;
  }


  editVendor(vendor) {
    this.componentOpticalStoreService.vendorData = vendor;
    const navigationExtras: NavigationExtras = {
      queryParams: {
         'isEditable': true
      }
  };
    this.router.navigate(['/opticalStore/optical-add-suppliers'], navigationExtras);
  }

  deleteVendor(vendor, index) {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: {title: 'Are You Sure Want To Delete Vendor ?'},
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
     if (result === true) {
       const deletePayload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        vendorId: vendor.vendorId
       };
      this.opticalService.deleteVendor(deletePayload).subscribe((response: any ) => {
      if (response.responseCode === 'E200') {
        this.vendorsList.splice(index, 1);
        if (index === 0) {
          this.currentSupplier = this.vendorsList[0];
          this.vendorsList[0].selected = true;
          // this.fetchManufacturerVendors(this.vendorsList[0]);
        }
        this.toaster.success('Success!', response.responseMessage);
      } else if (response.response === 'E400') {
        this.toaster.error('Failure!', response.responseMessage);
      }
    }, err => {
      this.toaster.error('Failure!', 'Something went wrong');
    });
     }
    });
  }

  backToAllVendors() {
    this.vendorsList = this.tempVendorsList;
    if (this.vendorsList.length > 0) {
      this.currentSupplier = this.vendorsList[0];
      this.vendorsList[0].selected = true;
    }
    this.backToAll = false;
  }
  onGridReady(params) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      vendorId: this.currentSupplier.vendorId

    };
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    this.fetchVendorInvoiceDetails(payload);
  }

  onSelectionChanged($event) {
    this.selectedRows = this.gridApi.getSelectedRows();
  }

  fetchVendorInvoiceDetails(payload){
    this.opticalService.fetchVendorInvoiceDetails(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.gridApi.setRowData(response.data);
          // this.toastr.success('Success!', response.responseMessage);
        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', response.responseMessage);
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

}
