import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { OpticalStoreService } from '@app/modules/optical-store/opticalStore.service';
import { OpticalStoreApiService } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { MatDatepicker } from '@angular/material';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl
} from '@angular/forms';
import { Observable, concat, of, Subject } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  switchMap,
  map,
  catchError
} from 'rxjs/operators';
import { DatePipe } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { GridOptions, GridApi, ColumnApi } from 'ag-grid/main';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
// @AutoUnsubscribe()
@Component({
  selector: 'app-optical-due-list',
  templateUrl: './optical-point-of-sale-due-list.component.html',
  styleUrls: ['./optical-point-of-sale-due-list.component.scss']
})
export class OpticalPointOfSaleDueListComponent implements OnInit, OnDestroy {
  private sessionData: UserSession;
  public posList = [];
  public posTansactionList: any[] = [];
  private fetchType;
  constructor(
    private componentOpticalStoreService: OpticalStoreService,
    private opticalService: OpticalStoreApiService,
    private toaster: ToastrService,
    private datePipe: DatePipe,
    private dialogRef: MatDialogRef<OpticalPointOfSaleDueListComponent>,
    userService: UserService,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    this.sessionData = userService.getUser();
  }
  ngOnInit() {
    this.fetchPointOfSaleItemsAndLensDetails();
    if (!this.data.keyFrom) {
      this.fetchPosTransactionHistory();
    }
  }

  fetchPointOfSaleItemsAndLensDetails() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      pointOfSaleId: this.data.pointOfSaleId
    };
    let url: string;
    if (this.data.keyFrom) {
      payload[
        'opticalPointOfSaleReturnId'
      ] = this.data.opticalPointOfSaleReturnId;
      url = 'fetchPosReturnDetails';
      this.fetchType = 'returnsHistory';
    } else {
      payload['opticalBillNo'] = this.data.opticalBillNo;
      url = 'fetchPointOfSaleItemsAndLensDetails';
      this.fetchType = 'details';
    }
    this.opticalService[url](payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          if (this.fetchType === 'returnsHistory') {
            if (
              response.data &&
              response.data.posReturnFrameAndContactLensDetailsRes
            ) {
              this.posList =
                response.data.posReturnFrameAndContactLensDetailsRes;
            }
            if (response.data && response.data.posReturnsLensDetailsRes) {
              this.posList = this.posList.concat(
                response.data.posReturnsLensDetailsRes
              );
            }
          }
          if (this.fetchType !== 'returnsHistory') {
            if (
              response.data &&
              response.data.inventoryItemsForFramesAndContactLens
            ) {
              this.posList =
                response.data.inventoryItemsForFramesAndContactLens;
            }
            if (response.data && response.data.insertPointOfSaleLensReqAndRes) {
              this.posList = this.posList.concat(
                response.data.insertPointOfSaleLensReqAndRes
              );
            }
          }
        } else if (response.responseCode === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      },
      () => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  fetchPosTransactionHistory() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      opticalBillNo: this.data.opticalBillNo,
      typeOfDue: 'POS'
    };
    this.opticalService.fetchPosTransactionHistory(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.posTansactionList = response.data;
        } else if (response.responseCode === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  cancelModal() {
    this.dialogRef.close();
  }
  ngOnDestroy() {
     }
}
