import { InjectionToken } from '@angular/core';

export const opticalStoreConstants = {
    FRAMES_ID: 234,
    CONTACT_LENS_ID: 235,
    LENS_ID: 101
};

export interface IOpticalStoreConstants {
    FRAMES_ID: number;
    CONTACT_LENS_ID: number;
    LENS_ID: number;
}

export const opticalStoreConstantsFactory = () => opticalStoreConstants;

export const OPTICAL_STORE_CONSTANTS_TOKEN = new InjectionToken('optical store constants', {
    factory: opticalStoreConstantsFactory,
    providedIn: 'root'
});

