import {
  Component,
  OnInit,
  Input,
  OnChanges,
  SimpleChanges,
  Inject,
  OnDestroy
} from '@angular/core';
import { OpticalStoreService } from '@app/modules/optical-store/opticalStore.service';
import { ToastrService } from 'ngx-toastr';
import { NavigationExtras, Router } from '@angular/router';
import { MatDialog } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { CommentsDialogComponent } from '@shared/components/comments-dialog/comments-dialog.component';
import { OpticalStoreApiService, JSBARCODE } from '@core/services';
import { Bool } from 'aws-sdk/clients/inspector';
import { Observable, Subscription, concat, of, Subject } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  switchMap,
  map,
  catchError
} from 'rxjs/operators';
import { Store, select } from '@ngrx/store';
import { getOpticalInventory } from '@app/modules/optical-store/reducers';
import { UserService } from '@core/services/user.service';
import { UserSession } from '@core/models/user-session';
import { FetchOpticalInventorySuccess } from '@app/modules/optical-store/actions';
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from '@angular/forms';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
// @AutoUnsubscribe()
@Component({
  selector: 'app-showing-optical-inventory',
  templateUrl: './showing-optical-inventory.component.html',
  styleUrls: ['./showing-optical-inventory.component.scss']
})
export class ShowingOpticalInventoryComponent implements OnInit, OnChanges, OnDestroy {
  settingBarcodeForm: FormGroup;
  @Input()
  menuType: any;
  public objectKeys = Object.keys;
  inventoryList: any[] = [];
  lensInventoryList: any[] = [];
  barcodeList: any[] = [];
  barcodeImg: any[] = [];
  barcodeSettingsData: Boolean = false;
  clickedEdit: Boolean;
  currentInventory: any;
  opticalItemType: any;
  barcodeSettingsFlag: Boolean = false;
  showingDetails: Boolean = false;
  fetchedSettingData: Boolean = false;
  settingObj: any;
  detailsArray = [];
  lensData = {
    rightDist: [
      { distanceRightSph: '' },
      { distanceRightCyl: '' },
      { distanceRightAxis: '' },
      { distanceRightVa: '' },
      { distanceRightAddSph: '' }
    ],
    leftDist: [
      { distanceLeftSph: '' },
      { distanceLeftCyl: '' },
      { distanceLeftAxis: '' },
      { distanceLeftVa: '' },
      { distanceLeftAddSph: '' }
    ],
    rightNear: [
      { nearRightSph: '' },
      { nearRightCyl: '' },
      { nearRightAxis: '' },
      { nearRightVa: '' },
      { nearRightAddSph: '' }
    ],
    leftNear: [
      { nearLeftSph: '' },
      { nearLeftCyl: '' },
      { nearLeftAxis: '' },
      { nearLeftVa: '' },
      { nearLeftAddSph: '' }
    ]
  };
  brandNameList$: Observable<any>;
  brandNameListinput$ = new Subject<string>();
  public brandNameListSubject: Subject<any>;
  public inventoryList$: Observable<any>;
  public inventoryListSubscription: Subscription;
  public backToAll = false;
  private changes: any;
  public tempInventoryList;
  public tempLensInventoryList;
  private sessionData: UserSession;
  constructor(
    private formbuilder: FormBuilder,
    private fb: FormBuilder,
    private opticalService: OpticalStoreApiService,
    private componentOpticalStoreService: OpticalStoreService,
    private toaster: ToastrService,
    private router: Router,
    private dialog: MatDialog,
    private store: Store<any>,
    userService: UserService,
    @Inject(JSBARCODE) private jsbarcode
  ) {
    this.inventoryList$ = this.store.pipe(select(getOpticalInventory));
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    // this.settingObj = {
    //   // labelBottomMargin: 0,
    //   // labelLeftMargin: 4.5,
    //   // labelRightMargin: 0,
    //   // labelTopMargin: 2.6,
    //   // labelWidth: 73,
    //   // labelHeight: 22.5197,
    //   // labelTypeNm: 'Default'
    // };
    this.fetchBrandNames();

    this.settingBarcodeForm = this.formbuilder.group({
      labelTypeNm: this.formbuilder.control('', Validators.required),
      labelWidth: this.formbuilder.control('', [
        Validators.required,
        Validators.minLength(70),
        Validators.maxLength(73)
      ]),
      labelHeight: this.formbuilder.control('', Validators.required),
      labelLeftMargin: this.formbuilder.control('', Validators.required),
      labelRightMargin: this.formbuilder.control('', Validators.required),
      labelTopMargin: this.formbuilder.control('', Validators.required),
      labelBottomMargin: this.formbuilder.control('', Validators.required)
    });

    // this.fetchInventoryList();
    this.store.dispatch(
      new FetchOpticalInventorySuccess({ clickedOnSearch: false, data: [] })
    );
    this.inventoryListSubscription = this.inventoryList$.subscribe(
      inventoryListData => {
        this.backToAll = inventoryListData.data.clickedOnSearch ? true : false;
        if (inventoryListData.data.data.length > 0) {
          this.inventoryList = inventoryListData.data.data.filter(val => {
            return val.itemTypeId === this.opticalItemType.opticalTypeId;
          });
        } else {
          this.inventoryList = inventoryListData.data.data;
        }
      }
    );
    this.fetchSettingNames();
  }

  ngOnChanges(changes: SimpleChanges) {
    this.changes = changes.menuType.currentValue;
    if (changes.menuType && changes.menuType.currentValue) {
      this.opticalItemType = this.componentOpticalStoreService.getCurrentOpticalItem(
        this.menuType
      );
      this.store.dispatch(
        new FetchOpticalInventorySuccess({ clickedOnSearch: false, data: [] })
      );
      this.fetchInventoryList();
      this.fetchLensInventoryList();
      this.showingDetails = false;
      this.backToAll = false;
    }
  }

  fetchSettingNames() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      labelTypeNm: 'recommended'
    };
    this.opticalService.fetchBarCodeDetails(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.settingObj = response.data[0];
          this.settingBarcodeForm.patchValue(this.settingObj);
          // this.brandNameListSubject.next(response.data);
        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', response.responseMessage);
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  fetchInventoryList() {
    const payload: any = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    if (this.opticalItemType.opticalTypeId) {
      payload.itemTypeId = this.opticalItemType.opticalTypeId;
    } else {
      payload.itemTypeId = this.changes;
    }
    this.opticalService.fetchInventoryList<any>(payload).subscribe(
      (response) => {
        if (response.responseCode === 'E200') {
          this.inventoryList = response.data;
          this.tempInventoryList = response.data;
        } else if (response.responseCode === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  // fetch api for lens data

  fetchLensInventoryList() {
    const payload: any = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId

    };
    // if (this.opticalItemType.opticalTypeId) {
    //   payload.itemTypeId = this.opticalItemType.opticalTypeId;
    // } else {
    //   payload.itemTypeId = this.changes;
    // }
    this.opticalService.fetchOpticalInventoryLens<any>(payload).subscribe(
      (response) => {
        if (response.responseCode === 'E200') {
          this.lensInventoryList = response.data;
          this.tempLensInventoryList = response.data;
        } else if (response.responseCode === 'E400') {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }

  editInventory(inventory) {
    this.componentOpticalStoreService.inventoryData = inventory;
    const navigationExtras: NavigationExtras = {
      queryParams: {
        isEditable: true,
        menuType: this.menuType
      }
    };
    this.router.navigate(
      ['/opticalStore/optical-add-inventory'],
      navigationExtras
    );
  }

  deleteInventory(inventory, index) {
    const dialogRef = this.dialog.open(CommentsDialogComponent, {
      data: {
        title: 'Delete Inventory',
        data: 'Reason for Deleting Inventory'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const deletePayload = {
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          createdUsrId: this.sessionData.regId,
          opticalInventoryId: inventory.opticalInventoryId,
          deleteReason: result
        };
        this.opticalService.deleteOpticalInventory(deletePayload).subscribe(
          (response: any) => {
            if (response.responseCode === 'E200') {
              this.inventoryList.splice(index, 1);
              this.toaster.success('Success!', response.responseMessage);
            } else if (response.response === 'E400') {
              this.toaster.error('Failure!', response.responseMessage);
            }
          },
          err => {
            this.toaster.error('Failure!', 'Something went wrong');
          }
        );
      }
    });
  }

  // delete api for lens inventory data
  deleteLensInventory(inventory, index) {
    const dialogRef = this.dialog.open(CommentsDialogComponent, {
      data: {
        title: 'Delete Inventory',
        data: 'Reason for Deleting Inventory'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const deletePayload = {
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          createdUsrId: this.sessionData.regId,
          opticalLensInventoryId: inventory.opticalLensInventoryId,
          deleteReason: result
        };
        this.opticalService.deleteOpticalInventoryLens(deletePayload).subscribe(
          (response: any) => {
            if (response.responseCode === 'E200') {
              this.lensInventoryList.splice(index, 1);
              this.toaster.success('Success!', response.responseMessage);
            } else if (response.response === 'E400') {
              this.toaster.error('Failure!', response.responseMessage);
            }
          },
          err => {
            this.toaster.error('Failure!', 'Something went wrong');
          }
        );
      }
    });
  }

  barcodeBtnDis() {
    if (this.menuType === 'Frame' || this.menuType === 'Contact Lense') {
    this.barcodeList = this.inventoryList.filter((val, index) => {
      if (val.barcode) {
        return val.selected;
      }
    });
  } else if (this.menuType === 'Lense') {
    this.barcodeList = this.lensInventoryList.filter((val, index) => {
      if (val.barcode) {
        return val.selected;
      }
    });
  }
  }
  showInfo(item, pos) {
    this.currentInventory = item;
      if (this.menuType === 'Lense') {
      this.lensInventoryList.forEach((val, index) => {
           if (item.opticalLensInventoryId === val.opticalLensInventoryId) {
             val.selected = false;
             this.showingDetails = true;
           }
          });
    } else {
      this.inventoryList.forEach((val, index) => {
            if (item.opticalInventoryId === val.opticalInventoryId) {
              val.selected = false;
              this.showingDetails = true;
            }
          });
    }
    if (this.menuType === 'Frame') {
      this.detailsArray = [
        { name: 'Barcode', model: 'barcode' },
        { name: 'Manufacturer', model: 'manufacturerNm' },
        { name: 'Brand Name', model: 'itemBrandNm' },
        { name: 'Serial No', model: 'serialNo'},
        { name: 'Model No', model: 'modelNo'},
        { name: 'On Hand', model: 'availableQty' },
        { name: 'Shape', model: 'frameShape' },
        { name: 'Size', model: 'frameSize' },
        { name: 'Material', model: 'opticalMaterialNm' },
        { name: 'Color', model: 'frameColor' },
        { name: 'Type', model: 'frameTypeNm' },
        { name: 'Gender', model: 'gender' },
        { name: 'Short Code', model: 'shortCode' },
        // { name: 'Description', model: 'description' }
      ];
    } else if (this.menuType === 'Contact Lense') {
      this.detailsArray = [
        { name: 'Barcode', model: 'barcode' },
        { name: 'Manufacturer', model: 'manufacturerNm' },
        { name: 'Brand Name', model: 'itemBrandNm' },
        { name: 'On Hand', model: 'availableQty' },
        { name: 'Color', model: 'frameColor' },
        { name: 'Short Code', model: 'shortCode' },
        // { name: 'Description', model: 'description' }
      ];
    } else if (this.menuType === 'Lense') {
      this.detailsArray = [
        { name: 'Barcode', model: 'barcode'},
        { name: 'Manufacturer', model: 'manufacturerNm'},
        { name: 'Brand Name', model: 'itemBrandNm'},
        { name: 'Lens Type', model: 'lensType'},
        { name: 'Coating Type', model: 'coatingType'},
        { name: 'Index', model: 'lensIndex'},
        { name: 'Coating', model: 'lensCoating'}
        ];
    }
    // } else {
    //   //this.showingDetails = false;
    // }
  }

  hideViewDetailsDiv() {
    this.showingDetails = false;
  }

  backToAllInventory() {
    this.inventoryList = this.tempInventoryList;
    this.lensInventoryList = this.tempLensInventoryList;
    this.backToAll = false;
  }

  barcodeGenerate() {
    let totalcostPrice = [];
    this.barcodeImg = [];
    totalcostPrice = this.barcodeList.reduce((acc, val) => {
      return [
        ...acc,
        ...Array.from(new Array(val.availableQty)).map(() => val)
      ];
    }, []);

    for (let i = 0; i < totalcostPrice.length; i++) {
      const barcodeObj: any = {
        ...this.settingObj
      };
      const canvas = document.createElement('canvas');
     this.jsbarcode(canvas, totalcostPrice[i].barcode, {
        format: 'code39',
        height: 1000,
        displayValue: false
      });
      barcodeObj.barcode = totalcostPrice[i].barcode.toUpperCase();
      barcodeObj.imageObj = canvas.toDataURL('image/jpeg', 1);
      barcodeObj.itemBrandNm = totalcostPrice[i].itemBrandNm;
      barcodeObj.brandMrp = totalcostPrice[i].brandMrp;
      barcodeObj.frameShape = totalcostPrice[i].frameShape;
      barcodeObj.frameColor = totalcostPrice[i].frameColor;
      barcodeObj.manufacturerNm = totalcostPrice[i].manufacturerNm;
      barcodeObj.modalNo = totalcostPrice[i].modelNo;

      this.barcodeImg.push(barcodeObj);
    }
    this.componentOpticalStoreService.getBarcodePrints(this.barcodeImg);
    this.barcodeList = [];
    if (this.menuType === 'Frame' || this.menuType === 'Contact Lense') {
    this.inventoryList.forEach(val => {
      return (val.selected = false);
    });
  } else if (this.menuType === 'Lense') {
    this.lensInventoryList.forEach(val => {
      return (val.selected = false);
    });
  }
  }

  // settingsBreak() {

  // }
  barcodeSettings() {
    this.barcodeSettingsFlag = true;
  }
  cancelOrgGroupForm() {
    this.barcodeSettingsFlag = false;
  }

  onAddBrand(brand) {
    this.settingBarcodeForm.patchValue(brand);
    if (brand.barCodeSettingsId) {
      this.fetchedSettingData = true;
    } else {
      this.settingObj = {};
      this.fetchedSettingData = false;
    }
  }

  fetchBrandNames() {
    this.brandNameListSubject = new Subject();
    this.brandNameList$ = concat(
      of([]), // default items
      this.brandNameListinput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        filter(checkStringCount => {
          if (checkStringCount.length > 1) {
            return true;
          }
        }),
        //  tap(() => this.people3Loading = true),
        switchMap((term: string) => {
          const payload = {
            createdUsrId: this.sessionData.regId,
            orgGrpId: this.sessionData.orgGroupId,
            orgId: this.sessionData.orgId,
            labelTypeNm: term
          };
          return this.opticalService.fetchBarCodeDetails(payload).pipe(
            map((response: any) => {
              if (response.responseCode === 'E200') {
                return response.data;
              } else if (response.response === 'E400') {
                // return (this.manufacturersList = []);
              }
            }),
            catchError(error => {
              return of([]);
            })
          );
        })
      )
    );
    this.brandNameList$.subscribe(this.brandNameListSubject);
  }

  saveBarcodeSetting() {
    if (this.fetchedSettingData || this.settingObj.barCodeSettingsId) {
      this.settingObj = this.settingBarcodeForm.value;
      this.toaster.success('Success!', 'Successfully changed your settings');
      return;
    }
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      noOfRollColumns: 1,
      ...this.settingBarcodeForm.value
    };
    this.settingObj = this.settingBarcodeForm.value;
    this.opticalService.insertOrUpdateBarCodeDetails(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.barcodeSettingsData = response.data;
          this.toaster.success('Success!', response.responseMessage);
        } else if (response.response === 'E400') {
          this.toaster.error('Failure!', response.responseMessage);
        }
      },
      err => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }
  ngOnDestroy() {
  }

}
