import { OpticalActions, OpticalActionTypes } from '@app/modules/optical-store/actions';

const initialState = {
  isLoading: false,
  errorOccured: false,
  data : []
};

export function opticalItemsReducer(state = initialState, action: OpticalActions) {
  switch (action.type) {
    case OpticalActionTypes.fetchOpticalItemTypes:
      return {
        isLoading: true,
        errorOccured: false,
        data : []
      };
    case OpticalActionTypes.fetchOpticalItemTypesSuccess:
     return {
        isLoading: false,
        errorOccured: false,
        data : action.payload
      };
    case OpticalActionTypes.fetchOpticalItemTypesError:
    return {
      isLoading: false,
      errorOccured: true,
      data : []
    };
    default:
      return state;
  }
}
