import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-add-optical-inventory',
  templateUrl: './add-optical-inventory.component.html',
  styleUrls: ['./add-optical-inventory.component.scss']
})
export class AddOpticalInventoryComponent implements OnInit {
  public inventoryType = 'Frame';
  menuType: any;
  constructor(
    private activatedRoute: ActivatedRoute,
  ) {
    this.activatedRoute.queryParams.subscribe(params => {
      if (params['menuType']) {
        this.inventoryType = params['menuType'];
      }
    });
  }

  ngOnInit() {
  }

}
