import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  Router,
  ActivatedRoute
} from '@angular/router';
import { UserSession } from '@core/models/user-session';
import { OpticalStoreService } from '../../opticalStore.service';
import {
  OpticalStoreApiService,
  OpdApiService,
  LoginApiService
} from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { Store } from '@ngrx/store';
import {
  FetchPatient,
  FetchOpticalItemTypes,
  FetchPatientSuccess,
  FetchOpticalVendorSuccess,
  FetchOpticalInventorySuccess
} from '@app/modules/optical-store/actions';

import { OsDialogComponent } from '@app/modules/optical-store/containers/os-dialog/os-dialog.component';
import { MatDialog } from '@angular/material';
import { UserService } from '@core/services/user.service';
import { Subscription } from 'rxjs';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
// @AutoUnsubscribe()
@Component({
  selector: 'app-optical-common',
  templateUrl: './optical-common.component.html',
  styleUrls: ['./optical-common.component.scss']
})
export class OpticalCommonComponent implements OnInit , OnDestroy {
  menuType = 'pos';
  // private sessionData = userService.getUser();
  private sessionData: UserSession;
  honorificsList: any = [];
  patientId: any;
  mobileAadhar: any;
  supplierName: any;
  brandName: any;
  currentSubMenu: any;
  private subscription: Subscription;
  isSaleActive = true;

  constructor(
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private componentOpticalService: OpticalStoreService,
    private opticalService: OpticalStoreApiService,
    private toaster: ToastrService,
    private opdService: OpdApiService,
    private store: Store<any>,
    private loginService: LoginApiService,
    private dialog: MatDialog,
    userService: UserService,
    private opticalStoreService: OpticalStoreService
  ) {
    this.menuType = 'pos';
    this.sessionData = userService.getUser();
    // this.parseSessionData(
    //   this.activatedRoute.snapshot.queryParamMap.get('data')
    // );
    this.router.navigate(['/opticalStore/optical-point-of-sale']);
  }

  ngOnInit() {
    this.getOpticalItems();
    this.componentOpticalService.getOpticalItemTypes();
  }

  getOpticalItems() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      opticalType: 'Item Type'
    };
    this.store.dispatch(new FetchOpticalItemTypes(payload));
  }

  searchPatient(val, type) {
    if (type === 'patientId') {
      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        hosPatientId: val
      };
      this.store.dispatch(new FetchPatient(payload));
    } else if (type === 'mobileAadhar') {
      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        mobileNoOrAadhar: val
      };
      if (this.subscription) {
        this.subscription.unsubscribe();
      }
      this.subscription = this.opticalService.fetchPatientOptical(payload)
        .subscribe(
          (response: any) => {
            if (response.responseCode === 'E200') {
              this.mobileAadhar = '';
              this.patientId = '';

              if (response.data.length > 1) {
                const dialogRef = this.dialog.open(OsDialogComponent, {
                  data: {
                    type: 'patientList',
                    patientList: response.data
                  },
                  disableClose: true
                });

                dialogRef.afterClosed().subscribe(result => {
                  if (result) {
                    this.store.dispatch(new FetchPatientSuccess([result]));
                    this.componentOpticalService.patientData.next([result]);
                    // const x = new FetchOpticalItemTypesSuccess(result);
                  }
                });
              } else if (response.data.length === 1) {
                this.store.dispatch(new FetchPatientSuccess(response.data));
                this.componentOpticalService.patientData.next(response.data);
              } else if (response.data.length === 0) {
                this.toaster.warning('No Data Found');
              }
            } else if (response.response === 'E400') {
              this.toaster.error('Failure!', response.responseMessage);
            }
          },
          err => {
            this.toaster.error('Failure!', 'Something went wrong');
          }
        );
    }
    this.patientId = '';
  }

  vendorSearch(vendorName) {
    if (vendorName) {
      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        vendorNm: vendorName,
        isFullDetails: 'true'
      };
      this.opticalService.fetchVendorsList(payload).subscribe(
        (response: any) => {
          const respObj = {
            clickedOnSearch: true,
            data: response.data
          };
          this.store.dispatch(new FetchOpticalVendorSuccess(respObj));
          this.supplierName = '';
        },
        err => {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      );
    }
  }

  brandNameSearch(brand) {
    if (brand) {
      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        itemBrandNm: brand
      };

      this.opticalService.fetchInventoryList(payload).subscribe(
        (response: any) => {
          const respObj = {
            clickedOnSearch: true,
            data: response.data
          };
          this.store.dispatch(new FetchOpticalInventorySuccess(respObj));
          this.brandName = null;
        },
        err => {
          this.toaster.error('Failure!', 'Something went wrong');
        }
      );
    }
  }

  clickedSubMenu(subMenuNm) {
    this.currentSubMenu = subMenuNm;
    if (subMenuNm !== 'productSale') {
      this.patientId = '';
      this.mobileAadhar = '';
    }
    if (subMenuNm !== 'allSuppliers') {
      this.supplierName = '';
    }
    if (subMenuNm !== 'allInventory') {
      this.brandName = '';
    }
    if (subMenuNm === 'pointOfSale' || subMenuNm === 'productSale') {
      this.isSaleActive = true;
    } else {
      this.isSaleActive = false;
    }
    this.componentOpticalService.opticalCommonData.next(subMenuNm);
  }
  ngOnDestroy() {
  }

}
